import * as d3 from 'd3';
import { getRandomId } from '../function/common.function';
import { Size } from '../interface/common.interface';
import { RootSvg } from './root-svg';

/**
 * 基礎圖表 包含圖表共用資訊
 */
export abstract class RootChart extends RootSvg {

  constructor(selector: string) {
    super(selector);
  }


  protected abstract setFactory(): void;

  override draw(useAnimation: boolean) {
    this.svg.attr('width', this.size.width).attr('height', this.size.height);
    this.drawChart(useAnimation);
  }

  /** 繪製圖表 */
  abstract drawChart(useAnimation?: boolean): void;
}
