import { GridFactory } from '../../factory/grid/grid/grid-factory';
import { GridlineFactory } from '../../factory/grid/gridline/gridline-factory';
import { AreaFactory } from '../../factory/grid/line/area-factory/area-factory';
import { DotFactory } from '../../factory/grid/line/dot-factory/dot-factory';
import { LineFactory } from '../../factory/grid/line/line-factory/line-factory';
import { DataSetWithDataArray } from '../../interface/common.interface';
import { RootChart } from '../root-chart';

/** 基礎區域圖 */
export class AreaChart extends RootChart {
  private gridFactory: GridFactory;
  private gridlineFactory!: GridlineFactory;
  private lineFactory!: LineFactory;
  private dotFactory!: DotFactory;
  private areaFactory!: AreaFactory;

  private lineDataSetWithDataArray: DataSetWithDataArray[] = [];
  private areaDataSetWithDataArray: DataSetWithDataArray[] = [];
  private isUseGridlineFactory: boolean = true;
  private isUseLineFactory: boolean = true;
  private isUseDotFactory: boolean = true;
  private isUseAreaFactory: boolean = true;

  constructor(selector: string) {
    super(selector);
    this.gridFactory = new GridFactory(this);
    this.setFactory();
  }

  protected setFactory() {
    this.gridlineFactory = this.gridlineFactory
      ? this.gridlineFactory.setGridFactory(this.gridFactory)
      : new GridlineFactory(this.gridFactory);

    this.areaFactory = this.areaFactory
      ? this.areaFactory.setGridFactory(this.gridFactory)
      : new AreaFactory(this.gridFactory);

    this.areaFactory.setDataSets(this.areaDataSetWithDataArray);

    this.lineFactory = this.lineFactory
      ? this.lineFactory.setGridFactory(this.gridFactory)
      : new LineFactory(this.gridFactory);

    this.lineFactory.setDataSets(this.lineDataSetWithDataArray);

    this.dotFactory = this.dotFactory
      ? this.dotFactory.setLineFactory(this.lineFactory)
      : new DotFactory(this.lineFactory);
  }

  /** 設定折線資料 */
  setLineDataSets(dataSets: DataSetWithDataArray[]): AreaChart {
    this.lineDataSetWithDataArray = dataSets;
    const gridDataSetting = this.lineDataSetWithDataArray.map(item => {
      return { dataSetWithDataArray: item, isStack: false, axisIndex: 0 };
    });
    this.gridFactory.setDataSetWithDataArrays(gridDataSetting);
    return this;
  }

  /** 設定區域資料 */
  setAreaDataSets(dataSets: DataSetWithDataArray[]): AreaChart {
    this.areaDataSetWithDataArray = dataSets;
    const gridDataSetting = this.lineDataSetWithDataArray.map(item => {
      return { dataSetWithDataArray: item, isStack: false, axisIndex: 0 };
    });
    this.gridFactory.setDataSetWithDataArrays(gridDataSetting);
    return this;
  }

  /** 設定要使用的 GridFactory */
  setGridFactory(gridFactory: GridFactory): AreaChart {
    this.gridFactory = gridFactory;
    return this;
  }

  /** 設定要使用的 GridlineFactory */
  setGridlineFactory(gridlineFactory: GridlineFactory): AreaChart {
    this.gridlineFactory = gridlineFactory;
    return this;
  }

  /** 設定要使用的 LineFactory */
  setLineFactory(lineFactory: LineFactory): AreaChart {
    this.lineFactory = lineFactory;
    return this;
  }

  /** 設定要使用的 AreaFactory */
  setAreaFactory(areaFactory: AreaFactory): AreaChart {
    this.areaFactory = areaFactory;
    return this;
  }

  getGridFactory(): GridFactory {
    return this.gridFactory;
  }

  getGridlineFactory(): GridlineFactory {
    return this.gridlineFactory;
  }

  getLineFactory(): LineFactory {
    return this.lineFactory;
  }

  getDotFactory(): DotFactory {
    return this.dotFactory;
  }

  getAreaFactory(): AreaFactory {
    return this.areaFactory;
  }

  useGridlineFactory(isUseGridlineFactory: boolean): AreaChart {
    this.isUseGridlineFactory = isUseGridlineFactory;
    return this;
  }

  useLineFactory(isUseLineFactory: boolean): AreaChart {
    this.isUseLineFactory = isUseLineFactory;
    return this;
  }

  useDotFactory(isUseDotFactory: boolean): AreaChart {
    this.isUseDotFactory = isUseDotFactory;
    return this;
  }

  useAreaFactory(isUseAreaFactory: boolean): AreaChart {
    this.isUseAreaFactory = isUseAreaFactory;
    return this;
  }

  drawChart(useAnimation: boolean = false) {
    this.gridFactory.draw(useAnimation);
    this.setFactory();

    setTimeout(() => {
      this.isUseGridlineFactory ? this.gridlineFactory.draw(useAnimation) : this.gridlineFactory.clear(useAnimation);
      this.isUseAreaFactory ? this.areaFactory.draw(useAnimation) : this.areaFactory.clear(useAnimation);
      this.isUseLineFactory ? this.lineFactory.draw(useAnimation) : this.lineFactory.clear(useAnimation);
      this.isUseDotFactory ? this.dotFactory.draw(useAnimation) : this.dotFactory.clear(useAnimation);
    }, 0);
  }
}
