import { PieFactory } from './../../factory/radial/pie/pie-factory';
import { RadialFactory } from './../../factory/radial/radial/radial-factory';
import { RootChart } from '../root-chart';
import { DataSetWithData } from '../../interface/common.interface';

/** 基礎折線圖 */
export class PieChart extends RootChart {
  private radialFactory: RadialFactory;
  private pieFactory!: PieFactory;

  private pieDataSetWithData: DataSetWithData[] = [];

  constructor(selector: string) {
    super(selector);
    this.radialFactory = new RadialFactory(this);
    this.setFactory();
  }

  protected setFactory(): void {
    this.pieFactory = this.pieFactory
      ? this.pieFactory.setRadialFactory(this.radialFactory)
      : new PieFactory(this.radialFactory);
  }

  /** 設定資料 */
  setDataSets(dataSets: DataSetWithData[]): PieChart {
    this.pieDataSetWithData = dataSets;
    this.radialFactory.setData(dataSets);
    this.pieFactory.setData(dataSets);
    return this;
  }

  getRadialFactory(): RadialFactory {
    return this.radialFactory;
  }

  getPieFactory(): PieFactory {
    return this.pieFactory;
  }

  drawChart(useAnimation?: boolean): void {
    this.radialFactory.draw();
    setTimeout(() => {
      this.pieFactory.draw();
    }, 0);
  }
}
