import * as d3 from 'd3';
import { Color } from '../../../../config/color.config';
import { Direction } from '../../../../enum/enum';
import { getColorTypeArray, getSelectionColor } from '../../../../function/color.function';
import { deepCopy, getRandomId } from '../../../../function/common.function';
import { Data, DataSetWithDataArray } from '../../../../interface/common.interface';
import { BasicFactory } from '../../../basic-factory';
import { GridFactory } from '../../grid/grid-factory';
import { GridConfig } from '../../grid/grid.config';
import { GridState } from '../../grid/grid.state';
import { DefaultLineConfig, LineConfig } from './line.config';
import { CurveType } from '../../../../enum/curve.enum';

/** 折線組件，需繪製在 GridFactory 上 */
export class LineFactory extends BasicFactory<LineFactory> {
  private gridFactory: GridFactory;
  private gridState: GridState;
  private gridConfig: GridConfig;
  private dataSetWithDataArrays: DataSetWithDataArray[] = [];
  private dataColorArray: Color[][] = [];
  private lineArrayGroup: d3.Selection<d3.BaseType, unknown, HTMLElement, any>;
  private lineGroup!: d3.Selection<SVGElement | SVGGElement, DataSetWithDataArray, d3.BaseType, any>;
  private lineConfig: LineConfig = deepCopy(DefaultLineConfig);

  constructor(gridFactory: GridFactory) {
    super();
    this.gridFactory = gridFactory;
    this.gridState = gridFactory.getState();
    this.gridConfig = gridFactory.getConfig();
    this.lineArrayGroup = (
      this.gridState.gridGroup.select(`g#lineFactory_${this.factoryId}`).node()
        ? this.gridState.gridGroup.select(`g#lineFactory_${this.factoryId}`)
        : this.gridState.gridGroup.append('g').attr('id', `lineFactory_${this.factoryId}`)
    ) as d3.Selection<d3.BaseType, unknown, HTMLElement, any>;
  }

  /** 設定要被繪製在哪個 GridFactory 上 */
  setGridFactory(gridFactory: GridFactory): LineFactory {
    this.gridFactory = gridFactory;
    return this;
  }

  /** 設定折線資料 */
  setDataSets(dataSetWithDataArrays: DataSetWithDataArray[], axisIndex: number = 0): LineFactory {
    this.dataSetWithDataArrays = dataSetWithDataArrays;
    this.lineConfig.axisIndex = axisIndex;
    this.dataColorArray = getColorTypeArray(this.dataSetWithDataArrays);
    return this;
  }

  setConfig(lineConfig: Partial<LineConfig>): LineFactory {
    Object.assign(this.lineConfig, lineConfig);
    return this;
  }

  /** 設定折線粗細 */
  setStrokeWidth(width: number): LineFactory {
    this.lineConfig.strokeWidth = width;
    return this;
  }

  /** 設定曲線模式 */
  setCurveType(curveType: CurveType): LineFactory {
    this.lineConfig.curveType = curveType;
    return this;
  }

  /** 設定折線繪製時的動畫 */
  setEnterAnimation(enterAnimationType: LineAnimationType): LineFactory {
    this.lineConfig.enterAnimationType = enterAnimationType;
    return this;
  }

  /** 取得線段中資料點的 x 座標 */
  getXPosition(d: Data): number {
    const isVertical = this.gridConfig.direction == Direction.vertical;
    const dataX = (isVertical ? d.key : d.value) as {
      valueOf(): number;
    } & string;

    if (this.lineConfig.axisIndex == 0) {
      if (isVertical) {
        return this.gridState.xGridlineBottom(dataX) || 0;
      } else {
        return this.gridState.x(dataX) || 0;
      }
    } else {
      if (isVertical) {
        return this.gridState.xGridlineBottom(dataX) || 0;
      } else {
        return this.gridState.xTop(dataX) || 0;
      }
    }
  }

  /** 取的線段中資料點的 y 座標 */
  getYPosition(d: Data): number {
    const isVertical = this.gridConfig.direction == Direction.vertical;
    const dataY = (isVertical ? d.value : d.key) as {
      valueOf(): number;
    } & string;
    if (this.lineConfig.axisIndex == 0) {
      if (isVertical) {
        return this.gridState.y(dataY) || 0;
      } else {
        return this.gridState.yGridlineLeft(dataY) || 0;
      }
    } else {
      if (isVertical) {
        return this.gridState.yRight(dataY) || 0;
      } else {
        return this.gridState.yGridlineLeft(dataY) || 0;
      }
    }
  }

  getLineGroup(): d3.Selection<SVGElement | SVGGElement, DataSetWithDataArray, d3.BaseType, any> {
    return this.lineGroup;
  }

  getSelector(): string {
    return `g#lineFactory_${this.factoryId}`;
  }

  getConfig(): LineConfig {
    return this.lineConfig;
  }

  /** 取得 GridConfig */
  getGridConfig(): GridConfig {
    return this.gridConfig;
  }

  /** 取得 GridState */
  getState(): GridState {
    return this.gridState;
  }

  /** 取得折線資料 */
  getDataSetWithDataArrays(): DataSetWithDataArray[] {
    return this.dataSetWithDataArrays;
  }

  draw(useAnimation: boolean = false): void {
    this.gridState = this.gridFactory.getState();
    this.gridConfig = this.gridFactory.getConfig();
    // 資料缺失時補上null
    this.dataSetWithDataArrays.forEach(dataset => {
      // 將 dataset 的 data 轉為 Map 來方便檢查是否存在某 key
      const dataMap = new Map(dataset.data.map(item => [item.key, item]));
      // 生成新的 data 陣列，按照 allKeys 的順序補全缺失的 key
      dataset.data = this.gridState.keyScaleLabels.map(key => {
        if (dataMap.has(key)) {
          // 如果 key 存在，返回對應的 DataItem
          return dataMap.get(key)!;
        } else {
          // 如果 key 缺失，插入 { key: 缺失的 key, value: null }
          return { key, value: null };
        }
      });
    });

    const line = d3
      .line<Data>()
      .x(d => this.getXPosition(d))
      .y(d => this.getYPosition(d))
      .defined((d, i) => d.value !== null);

    const translate =
      this.gridConfig.direction == Direction.vertical
        ? `translateX(${(this.gridState.xGridlineBottom as any).bandwidth() / 2}px)`
        : `translateY(${(this.gridState.yGridlineLeft as any).bandwidth() / 2}px)`;
    this.lineArrayGroup
      .transition()
      .duration(useAnimation ? 500 : 0)
      .style('transform', translate);

    this.lineGroup = this.lineArrayGroup
      .selectAll<SVGElement, DataSetWithDataArray>('g.lineGroup')
      .data<DataSetWithDataArray>(this.dataSetWithDataArrays, d => d.label)
      .join(
        enter =>
          enter
            .append('g')
            .attr('class', `lineGroup`)
            .attr('id', (d, i) => `lineGroup_${this.factoryId}_${i}`),
        update => update,
        exit =>
          exit
            .transition()
            .duration(useAnimation ? 500 : 0)
            .style('opacity', 0)
            .remove()
      );

    const enterAnimation = this.getAnimation;
    const that = this;
    this.lineGroup.each(function (this: d3.BaseType, dsets: DataSetWithDataArray, i: number) {
      const setAttributes = (selection: any) => {
        selection
          .attr('d', line)
          .attr('fill', 'none')
          .attr('stroke', (d: DataSetWithDataArray, j: number) =>
            getSelectionColor(d3.select(this), that.dataColorArray[i], `line_${that.factoryId}`, j)
          )
          .attr('stroke-width', that.lineConfig.strokeWidth);
      };

      d3.select(this)
        .selectAll('path')
        .data([dsets.data])
        .join(
          enter =>
            enter
              .append('path')
              .attr('id', `line_${that.factoryId}_${i}`)
              .call(setAttributes)
              .call(enterAnimation.bind(that)),
          update =>
            update
              .transition()
              .duration(useAnimation ? 500 : 0)
              .call(setAttributes),
          exit => exit.remove()
        );
    });

    this.isFirstUpdate = false;
  }

  clear(useAnimation: boolean = false): void {
    this.lineArrayGroup
      .selectAll('path')
      .transition()
      .duration(useAnimation ? 500 : 0)
      .style('opacity', 0)
      .remove();
  }

  /** 判斷使用哪種動畫 */
  private getAnimation(selection: d3.Selection<SVGPathElement, Data[], any, unknown>) {
    switch (this.lineConfig.enterAnimationType) {
      case LineAnimationType.startToEnd:
        return this.animateStartToEnd(selection);
      case LineAnimationType.fadeIn:
        return this.animateFadeIn(selection);
      case LineAnimationType.none:
      default:
        return selection;
    }
  }

  /** line 動畫 從頭畫到尾 */
  private animateStartToEnd(
    selection: d3.Selection<SVGPathElement, Data[], SVGGraphicsElement, unknown>
  ): d3.Selection<SVGPathElement, Data[], SVGGraphicsElement, unknown> {
    return selection
      .attr('stroke-dasharray', function () {
        return `${this.getTotalLength()} ${this.getTotalLength()}`;
      })
      .attr('stroke-dashoffset', function () {
        return this.getTotalLength();
      })
      .call(selection => {
        selection
          .transition('animateStartToEnd')
          .duration(500)
          .attr('stroke-dashoffset', 0)
          .on('end', function () {
            d3.select(this).attr('stroke-dasharray', 'none');
          });
      });
  }

  /** line 動畫 淡入 */
  private animateFadeIn(
    selection: d3.Selection<SVGPathElement, Data[], SVGGraphicsElement, unknown>
  ): d3.Selection<SVGPathElement, Data[], SVGGraphicsElement, unknown> {
    return selection.attr('opacity', 0).call(selection => selection.transition().duration(500).attr('opacity', 1));
  }
}

/** LineFactory 的動畫種類 */
export enum LineAnimationType {
  none = 0,
  startToEnd = 1,
  fadeIn = 2
}
