import { Color, DefaultDataColorArray } from '../config/color.config';
import { Direction } from '../enum/enum';
import { DataSet } from '../interface/common.interface';

/** 回傳 d3.selection fill 中需要的顏色 */
export function getSelectionColor(
  selection: d3.Selection<any, any, any, any>,
  colors: Color[],
  elementId: string,
  elementIndex: number | string,
  direction: Direction = Direction.horizontal
): string {
  let returnColor = '';
  if (colors.length == 1) {
    returnColor =
      convertColourToColorType(colors[0].color).color +
      numberToHex(colors[0].opacity);
  } else {
    // 漸層色定義
    selection.selectAll(`defs.linearGradient[id^="${elementId}_"]`).remove();
    let lineGradient = selection
      .select(`defs.linearGradient#${elementId}_${elementIndex}`)
      .select(`linearGradient#gradient_${elementId}_${elementIndex}`) as d3.Selection<
      any,
      any,
      any,
      any
    >;
    if (lineGradient.empty()) {
      lineGradient = selection
        .append('defs')
        .attr('class', 'linearGradient')
        .attr('id', `${elementId}_${elementIndex}`)
        .datum(colors)
        .append('linearGradient');
    }

    lineGradient
      .attr('id', `gradient_${elementId}_${elementIndex}`)
      .attr('x1', '0%')
      .attr('x2', direction == Direction.horizontal ? '100%' : '0%')
      .attr('y1', '0%')
      .attr('y2', direction == Direction.horizontal ? '0%' : '100%');

    for (let i = 0; i < colors.length; i++) {
      lineGradient
        .append('stop')
        .attr('offset', (100 / (colors.length - 1)) * i + '%')
        .style('stop-color', colors[i].color)
        .style('stop-opacity', colors[i].opacity);
    }
    returnColor = `url(#gradient_${elementId}_${elementIndex})`;
  }

  return returnColor;
}

/** 回傳 css color 中需要的顏色 */
export function getCssColor(
  colors: Color[],
  direction: Direction = Direction.horizontal
) {
  let returnColor = '';

  if (colors.length == 1) {
    returnColor =
      convertColourToColorType(colors[0].color).color +
      numberToHex(colors[0].opacity);
  } else {
    returnColor = `linear-gradient(${
      direction == Direction.horizontal ? '90' : '180'
    }deg,${colors.map(
      (item) =>
        `${
          convertColourToColorType(item.color).color + numberToHex(item.opacity)
        }`
    )})`;
  }
  return returnColor;
}

/** 從 DataSet [ ] 中將 colors:string [ ] 轉換成 Color [ ] [ ] */
export function getColorTypeArray(DataSetWithDataArrays: DataSet[]): Color[][] {
  let colorTypeArray = [...DefaultDataColorArray];
  const colorArray = DataSetWithDataArrays.map((data) => {
    if (data.colors) {
      return data.colors.map((item) => {
        const color: Color = convertColourToColorType(item);
        return color;
      });
    } else {
      return [];
    }
  });

  for (let i = 0; i < colorArray.length; i++) {
    colorTypeArray[i] =
      colorArray[i].length < 1 ? DefaultDataColorArray[i] : colorArray[i];
  }

  return colorTypeArray;
}

function numberToHex(num: number): string {
  let hex = Math.round(num * 255).toString(16);

  if (hex.length == 1) {
    hex = '0' + hex;
  }

  return hex;
}

export function convertColourToColorType(colour: string): Color {
  const colorString = colourNameToHex(colour);
  return colourToColorType(colorString);
}

