import * as d3 from 'd3';
import { Observable, Subject } from 'rxjs';
import { Color } from '../../../../config/color.config';
import { FontConfig } from '../../../../config/font.config';
import { Direction } from '../../../../enum/enum';
import { getColorTypeArray, getSelectionColor } from '../../../../function/color.function';
import { deepCopy, getRandomId, getTextRect } from '../../../../function/common.function';
import { Data, DataSetWithDataArray } from '../../../../interface/common.interface';
import { BasicFactory } from '../../../basic-factory';
import { BasicTooltipFactory } from '../../../tooltip/basic-tooltip-factory';
import { TooltipFactory } from '../../../tooltip/tooltip-factory';
import { GridFactory } from '../../grid/grid-factory';
import { GridConfig } from '../../grid/grid.config';
import { GridState } from '../../grid/grid.state';
import { BarConfig, DefaultBarConfig } from './bar.config';

export interface PointeroverData {
  key: string | number | undefined;
  activeIndex: number;
  data: (Data | undefined)[];
  elements: Element[];
  event: PointerEvent;
}

/** 長條組件，需繪製在 GridFactory 上 */
export class BarFactory extends BasicFactory<BarFactory> {
  private gridFactory: GridFactory;
  private gridState: GridState;
  private gridConfig: GridConfig;
  private dataSetWithDataArrays: DataSetWithDataArray[] = [];
  private dataColorArray: Color[][] = [];
  private barArrayGroup: d3.Selection<d3.BaseType, unknown, HTMLElement, any>;
  private barGroup!: d3.Selection<d3.BaseType | SVGGElement, any, d3.BaseType, any>;
  private barConfig: BarConfig = deepCopy(DefaultBarConfig);
  private pointerover$: Subject<any> = new Subject();
  private pointerout$: Subject<any> = new Subject();

  constructor(
    gridFactory: GridFactory,
    private tooltipFactory: TooltipFactory<any> = new BasicTooltipFactory()
  ) {
    super();
    this.gridFactory = gridFactory;
    this.gridState = gridFactory.getState();
    this.gridConfig = gridFactory.getConfig();
    this.barArrayGroup = (
      this.gridState.gridGroup.select(`g#barFactory_${this.factoryId}`).node()
        ? this.gridState.gridGroup.select(`g#barFactory_${this.factoryId}`)
        : this.gridState.gridGroup.append('g').attr('id', `barFactory_${this.factoryId}`)
    ) as d3.Selection<d3.BaseType, unknown, HTMLElement, any>;
    this.tooltipFactory.setPositionStrategy({ x: 'right', y: 'center' });
  }

  /** 設定要被繪製在哪個 GridFactory 上 */
  setGridFactory(gridFactory: GridFactory): BarFactory {
    this.gridFactory = gridFactory;
    return this;
  }

  /** 設定長條資料 */
  setDataSets(dataSetWithDataArrays: DataSetWithDataArray[], axisIndex: number = 0): BarFactory {
    this.dataSetWithDataArrays = dataSetWithDataArrays;
    this.barConfig.axisIndex = axisIndex;
    this.dataColorArray = getColorTypeArray(this.dataSetWithDataArrays);
    return this;
  }

  setConfig(barConfig: Partial<BarConfig>): BarFactory {
    Object.assign(this.barConfig, barConfig);
    return this;
  }

  /** 設定長條繪製時的動畫 */
  setEnterAnimation(enterAnimationType: BarAnimationType): BarFactory {
    this.barConfig.enterAnimationType = enterAnimationType;
    return this;
  }

  /** 設定是否堆疊，若為否則平行呈現 */
  setStack(isStack: boolean): BarFactory {
    this.barConfig.isStack = isStack;
    return this;
  }

  /** 設定是否顯示數值標籤 */
  setShowValueLabel(showValueLabel: boolean): BarFactory {
    this.barConfig.showValueLabel = showValueLabel;
    return this;
  }

  /** 設定數值標籤文字樣式 */
  setValueLabelFont(valueLabelFontSetting: Partial<FontConfig>): BarFactory {
    Object.assign(this.barConfig.valueLabelFontSetting, valueLabelFontSetting);
    return this;
  }

  /** 設定數值標籤顯示的位置 */
  setIsValueLabelOutside(isValueLabelOutside: boolean): BarFactory {
    this.barConfig.isValueLabelOutside = isValueLabelOutside;
    return this;
  }

  /** 取的線段中資料點的 x 座標 */
  getXPosition(d: Data): number {
    const isVertical = this.gridConfig.direction == Direction.vertical;
    const dataX = (isVertical ? d.key : d.value) as {
      valueOf(): number;
    } & string;

    if (this.barConfig.axisIndex == 0) {
      if (isVertical) {
        return this.gridState.xGridlineBottom(dataX) || 0;
      } else {
        return this.gridState.x(dataX) || 0;
      }
    } else {
      return this.gridState.xTop(dataX) || 0;
    }
  }

  /** 取的線段中資料點的 y 座標 */
  getYPosition(d: Data): number {
    const isVertical = this.gridConfig.direction == Direction.vertical;
    const dataY = ((isVertical ? d.value : d.key) || 0) as {
      valueOf(): number;
    } & string;

    if (this.barConfig.axisIndex == 0) {
      if (isVertical) {
        return this.gridState.y(dataY) || 0;
      } else {
        return this.gridState.yGridlineLeft(dataY) || 0;
      }
    } else {
      return this.gridState.yRight(dataY) || 0;
    }
  }

  getSelector(): string {
    return `g#barFactory_${this.factoryId}`;
  }

  getConfig(): BarConfig {
    return this.barConfig;
  }

  /** 監聽 Pointerover 事件 */
  onPointerover(): Observable<PointeroverData> {
    return this.pointerover$.asObservable();
  }

  /** 監聽 Pointerout 事件 */
  onPointerout(): Observable<PointerEvent> {
    return this.pointerout$.asObservable();
  }

  draw(useAnimation: boolean = false): void {
    this.gridState = this.gridFactory.getState();
    this.gridConfig = this.gridFactory.getConfig();
    const categories = this.dataSetWithDataArrays.length > 0 ? this.dataSetWithDataArrays[0].data.map(d => d.key) : [];
    const keys = this.dataSetWithDataArrays.map(DataSetWithDataArray => DataSetWithDataArray.label);
    const stack = d3.stack().keys(keys).offset(d3.stackOffsetDiverging);
    const stackData: XValueOfBar[] = categories.map((category, i) => {
      let obj: any = { stackKeyName: category };
      this.dataSetWithDataArrays.forEach(d => {
        if (d.data[i]) {
          obj[d.label] = d.data[i].value;
        }
      });
      return obj;
    });
    const gap = 4;

    let positiveSumArray: number[] = [],
      negetiveSumArray: number[] = [];
    for (const singleData of stackData) {
      let positiveSum = 0,
        negetiveSum = 0;
      for (const key of Object.keys(singleData)) {
        if (key == 'stackKeyName') continue;
        if (singleData[key] > 0) {
          positiveSum += singleData[key];
        } else {
          negetiveSum += singleData[key];
        }
      }
      positiveSumArray.push(positiveSum);
      negetiveSumArray.push(negetiveSum);
    }

    // console.log(stackData);
    // console.log(stack(stackData));

    const groupData = this.barConfig.isStack
      ? stack(stackData)
      : (this.dataSetWithDataArrays.map(item => item.data) as any);

    this.barGroup = this.barArrayGroup
      .selectAll(`g`)
      .data(groupData)
      .join(
        enter => enter.append('g').attr('id', (d, i) => `barGroup_${this.factoryId}_${i}`),
        update => update,
        exit =>
          exit
            .transition()
            .duration(useAnimation ? 500 : 0)
            .style('opacity', 0)
            .remove()
      );

    const that = this;
    this.barGroup.each(function (this: d3.BaseType, dataArray: any, i: number) {
      const isVertical = that.gridConfig.direction == Direction.vertical;
      //#region function
      // 圓角遮罩
      const roundedMask = (
        selection: d3.Selection<any, any, any, any>,
        index: number,
        data: any,
        dataValue: number | string | null,
        posX: Function,
        posY: Function,
        barWidth: number,
        barHeight: Function
      ) => {
        const parent = d3.select(this);
        const id = `${that.factoryId}_${i}_${index}`;
        let clipPathRect: d3.Selection<any, unknown, null, undefined> = parent
          .select(`#bar_clipPath_rounded_${id}`)
          .select(`#rounded_${id}`)
          .select('rect');
        if (clipPathRect.empty()) {
          clipPathRect = parent
            .append('defs')
            .attr('id', `bar_clipPath_rounded_${id}`)
            .append('clipPath')
            .attr('id', `rounded_${id}`)
            .append('rect');
        }

        const dataValueNumber = Number(dataValue);
        clipPathRect
          .transition()
          .duration(useAnimation ? 500 : 0)
          .attr('x', () => {
            return posX(data) - (!isVertical && dataValueNumber >= 0 ? barHeight(data) * 0.5 : 0);
          })
          .attr('y', () => (isVertical ? posY(data) - (dataValueNumber < 0 ? barHeight(data) * 0.5 : 0) : posY(data)))
          .attr('width', () => (isVertical ? barWidth : barHeight(data) * 1.5))
          .attr('height', () => (isVertical ? barHeight(data) * 1.5 : barWidth))
          .attr('rx', that.barConfig.borderRadius)
          .attr('ry', that.barConfig.borderRadius);

        selection.attr('clip-path', () => `url(#rounded_${id})`);
      };

      // 平行呈現 d3.Selection<any, any, any, any>
      const setParallelBar = (selection: any) => {
        //#region ParallelBar attributes
        const gridBand = isVertical
          ? (that.gridState.xGridlineBottom as d3.ScaleBand<string>).bandwidth() * that.barConfig.bandwidth
          : (that.gridState.yGridlineLeft as d3.ScaleBand<string>).bandwidth() * that.barConfig.bandwidth;
        const barWidth = (gridBand - gap * (groupData.length - 1)) / groupData.length;

        const barHeight = function (d: Data) {
          return isVertical
            ? Math.abs(that.getYPosition(d) - that.getYPosition({ key: d.key, value: 0 }))
            : Math.abs(that.getXPosition(d) - that.getXPosition({ key: d.key, value: 0 }));
        };

        const posX = function (d: Data) {
          return isVertical
            ? that.getXPosition(d) + i * (barWidth + gap)
            : that.getXPosition({
                key: d.key,
                value: Math.max(0, Number(d.value))
              }) - barHeight(d);
        };

        const posY = function (d: Data) {
          return isVertical
            ? that.getYPosition({
                key: d.key,
                value: Math.max(0, Number(d.value))
              })
            : that.getYPosition(d) + i * (barWidth + gap);
        };
        //#endregion

        selection
          .attr(
            'fill',
            getSelectionColor(
              d3.select(this),
              that.dataColorArray[i],
              `bar_${that.gridState.id}`,
              i,
              that.dataSetWithDataArrays[i].gradientDirection
            )
          )
          .attr('x', (d: Data) => posX(d))
          .attr('y', (d: Data) => posY(d))
          .attr('width', (d: Data) => (isVertical ? barWidth : barHeight(d)))
          .attr('height', (d: Data) => (isVertical ? barHeight(d) : barWidth))
          .call((selection: d3.Selection<any, any, any, any>) => {
            selection.each(function (this: any, data: Data, index: number) {
              roundedMask(d3.select(this), index, data, data.value, posX, posY, barWidth, barHeight);
              if (that.barConfig.showValueLabel) {
                setValueLabel(index, data, data.value, posX, posY, barWidth, barHeight);
              }
            });
          });
      };

      const setValueLabel = (
        index: number,
        data: any,
        dataValue: number | string | null,
        posX: Function,
        posY: Function,
        barWidth: number,
        barHeight: Function
      ) => {
        const parent = d3.select(this);
        const id = `${that.factoryId}_${i}_${index}`;

        let textSelection: d3.Selection<any, unknown, null, undefined> = parent.select(`#bar_valueLabel_${id}`);

        if (textSelection.empty()) {
          textSelection = parent.append('text').attr('id', `bar_valueLabel_${id}`);
        }

        const dataValueNumber = Number(dataValue);
        const labelRect = getTextRect(
          dataValueNumber.toString(),
          that.barConfig.valueLabelFontSetting['font-family'],
          that.barConfig.valueLabelFontSetting['font-size'],
          that.barConfig.valueLabelFontSetting['font-weight']
        );

        const gap = 8;
        const deltaX = isVertical
          ? barWidth / 2 - labelRect.width / 2
          : dataValueNumber >= 0
            ? barHeight(data) + (that.barConfig.isValueLabelOutside ? gap : -labelRect.width - gap)
            : that.barConfig.isValueLabelOutside ? -labelRect.width - gap : gap;

        // const deltaY = barWidth / 2 + labelRect.height / 2 - gap;
        const deltaY = isVertical
        ? dataValueNumber >= 0
          ? (that.barConfig.isValueLabelOutside ? -gap : labelRect.height )
          : barHeight(data) + (that.barConfig.isValueLabelOutside ? labelRect.height : -gap )
        : barWidth / 2 + labelRect.height / 2 - gap;

        textSelection
          .transition()
          .duration(useAnimation ? 500 : 0)
          .attr('x', () => posX(data) + deltaX)
          .attr('y', () => posY(data) + deltaY)
          .attr('fill', that.barConfig.valueLabelFontSetting.color)
          .attr('font-family', that.barConfig.valueLabelFontSetting['font-family'])
          .attr('font-size', that.barConfig.valueLabelFontSetting['font-size'])
          .attr('font-weight', that.barConfig.valueLabelFontSetting['font-weight'])
          .text(dataValueNumber);
      };

      // 堆疊呈現
      const setStackBar = (selection: any) => {
        //#region StackBar attributes
        const barWidth = isVertical
          ? (that.gridState.xGridlineBottom as any).bandwidth()
          : (that.gridState.yGridlineLeft as any).bandwidth();

        const barHeight = function (d: StackDataType) {
          return isVertical
            ? Math.abs(
                that.getYPosition({ key: d.data.stackKeyName, value: d[0] }) -
                  that.getYPosition({ key: d.data.stackKeyName, value: d[1] })
              )
            : Math.abs(
                that.getXPosition({ key: d.data.stackKeyName, value: d[0] }) -
                  that.getXPosition({ key: d.data.stackKeyName, value: d[1] })
              );
        };

        const posX = function (d: StackDataType) {
          return isVertical
            ? that.gridState.xGridlineBottom(d.data.stackKeyName as any) || 0
            : that.getXPosition({ key: d.data.stackKeyName, value: d[1] }) - barHeight(d);
        };

        const posY = function (d: StackDataType) {
          return isVertical
            ? that.getYPosition({ key: d.data.stackKeyName, value: d[1] })
            : that.gridState.yGridlineLeft(d.data.stackKeyName as any) || 0;
        };
        //#endregion

        selection
          .attr(
            'fill',
            getSelectionColor(
              d3.select(this),
              that.dataColorArray[i],
              `bar_${that.gridState.id}`,
              i,
              that.dataSetWithDataArrays[i].gradientDirection
            )
          )
          .attr('x', (d: StackDataType) => posX(d))
          .attr('y', (d: StackDataType) => posY(d))
          .attr('width', (d: StackDataType) => (isVertical ? barWidth : barHeight(d)))
          .attr('height', (d: StackDataType) => (isVertical ? barHeight(d) : barWidth))
          .call((selection: d3.Selection<any, any, any, any>) => {
            selection.each(function (this: any, data: StackDataType, index: number) {
              const key = dataArray.key;
              if ((data[0] < 0 && data[0] == negetiveSumArray[index]) || data[1] == positiveSumArray[index]) {
                roundedMask(d3.select(this), index, data, data.data[key], posX, posY, barWidth, barHeight);
                if (that.barConfig.showValueLabel) {
                  setValueLabel(index, data, data.data[key], posX, posY, barWidth, barHeight);
                }
              }

            });
          });
      };

      // 單行提示框
      const singleTooltip = (selection: d3.Selection<any, any, d3.BaseType, unknown>) => {
        selection
          .on('pointerover', function (event: PointerEvent, d: Data) {
            that.tooltipFactory.setPositionStrategy({
              x: 'center',
              y: Number(d.value) > 0 ? 'top' : 'bottom'
            });
            const element = event.currentTarget as HTMLElement;
            const j = that.dataSetWithDataArrays[i].data.findIndex(item => item.key == d.key);

            // 取得所有 barGroup 中相同 index 的 bar
            let elements: Element[] = [];
            for (let index = 0; index < that.dataSetWithDataArrays.length; index++) {
              const el = d3.select(`rect#bar_${that.gridState.id}_${index}_${j}`).node() as Element;
              elements.push(el);
            }

            const pointeroverData: PointeroverData = {
              key: d.key,
              activeIndex: i,
              data: that.dataSetWithDataArrays.map(item => item.data[j]),
              elements: elements,
              event: event
            };
            that.pointerover$.next(pointeroverData);

            if (!that.barConfig.showTooltip) return;
            that.tooltipFactory.setElement(element).setColor([that.dataColorArray[i]]).setData([d]).draw();
          })
          .on('pointermove', function (event: PointerEvent) {
            d3.select(this).dispatch('grid-hover', event);
          })
          .on('pointerout', function (event: PointerEvent, d: Data) {
            that.pointerout$.next(event);
            if (!that.barConfig.showTooltip) return;
            that.tooltipFactory.clear();
          });
      };

      // 多行提示框
      const multiTooltip = (selection: d3.Selection<any, any, d3.BaseType, unknown>) => {
        selection
          .on('pointerover', function (event: PointerEvent, d: StackDataType) {
            d3.select(this)
              .transition()
              .duration(useAnimation ? 500 : 0)
              .ease(d3.easeCubicInOut)
              .attr('r', 1.5);
            const element = event.currentTarget as HTMLElement;
            const tooltipDataArray: Data[] = [];
            for (const key of Object.keys(d.data)) {
              if (key !== 'stackKeyName') {
                tooltipDataArray.push({ key: key, value: d.data[key] });
              }
            }
            that.tooltipFactory.setConfig({ activeIndex: i });
            const pointeroverData: PointeroverData = {
              key: d.data['stackKeyName'],
              activeIndex: i,
              data: tooltipDataArray,
              elements: [element],
              event: event
            };
            that.pointerover$.next(pointeroverData);
            if (!that.barConfig.showTooltip) return;
            that.tooltipFactory.setElement(element).setColor(that.dataColorArray).setData(tooltipDataArray).draw();
          })
          .on('pointermove', function (event: PointerEvent) {
            d3.select(this).dispatch('grid-hover', event);
          })
          .on('pointerout', function (event: PointerEvent, d: StackDataType) {
            d3.select(this)
              .transition()
              .duration(useAnimation ? 500 : 0)
              .attr('r', 1);
            that.pointerout$.next(event);
            if (!that.barConfig.showTooltip) return;
            that.tooltipFactory.clear();
          });
      };
      //#endregion

      d3.select(this)
        .selectAll('rect.bar')
        .data(dataArray as any, (d: any) => d.key)
        .join(
          enter =>
            enter
              .append('rect')
              .attr('class', 'bar')
              .attr('id', (d, j) => `bar_${that.gridState.id}_${i}_${j}`)
              .call(that.barConfig.isStack ? setStackBar : setParallelBar)
              .call(that.barConfig.isStack ? multiTooltip : singleTooltip),
          // .call(enterAnimation.bind(that))
          update =>
            update
              .transition()
              .duration(useAnimation ? 500 : 0)
              .call(that.barConfig.isStack ? setStackBar : setParallelBar),
          exit => exit.remove()
        );
    });

    this.isFirstUpdate = false;
  }

  clear(useAnimation: boolean = false): void {
    this.barArrayGroup
      .selectAll('rect')
      .transition()
      .duration(useAnimation ? 500 : 0)
      .style('opacity', 0)
      .remove();

    this.barArrayGroup
      .selectAll('defs')
      .transition()
      .duration(useAnimation ? 500 : 0)
      .style('opacity', 0)
      .remove();
  }

  /** 判斷使用哪種動畫 */
  private getAnimation(selection: d3.Selection<SVGPathElement, Data[], any, unknown>) {
    switch (this.barConfig.enterAnimationType) {
      case BarAnimationType.startToEnd:
        return this.animateStartToEnd(selection);
      case BarAnimationType.fadeIn:
        return this.animateFadeIn(selection);
      case BarAnimationType.none:
      default:
        return selection;
    }
  }

  /** line 動畫 從頭畫到尾 */
  private animateStartToEnd(
    selection: d3.Selection<SVGPathElement, Data[], SVGGraphicsElement, unknown>
  ): d3.Selection<SVGPathElement, Data[], SVGGraphicsElement, unknown> {
    return selection
      .attr('stroke-dasharray', function () {
        return `${this.getTotalLength()} ${this.getTotalLength()}`;
      })
      .attr('stroke-dashoffset', function () {
        return this.getTotalLength();
      })
      .call(selection => {
        selection
          .transition('animateStartToEnd')
          .duration(500)
          .attr('stroke-dashoffset', 0)
          .on('end', function () {
            d3.select(this).attr('stroke-dasharray', 'none');
          });
      });
  }

  /** line 動畫 淡入 */
  private animateFadeIn(
    selection: d3.Selection<SVGPathElement, Data[], SVGGraphicsElement, unknown>
  ): d3.Selection<SVGPathElement, Data[], SVGGraphicsElement, unknown> {
    return selection.attr('opacity', 0).call(selection => selection.transition().duration(1000).attr('opacity', 1));
  }
}

interface XValueOfBar {
  stackKeyName: string;
  [key: string]: any;
}

interface StackDataType {
  0: number;
  1: number;
  data: XValueOfBar;
}

/** BarFactory 的動畫種類 */
export enum BarAnimationType {
  none = 0,
  startToEnd = 1,
  fadeIn = 2
}
