import { BarFactory } from './../../factory/grid/bar/bar-factory/bar-factory';
import { GridFactory } from '../../factory/grid/grid/grid-factory';
import { DotFactory } from '../../factory/grid/line/dot-factory/dot-factory';
import { LineFactory } from '../../factory/grid/line/line-factory/line-factory';
import { DataSetWithDataArray } from '../../interface/common.interface';
import { RootChart } from '../root-chart';
import { GridlineFactory } from '../../factory/grid/gridline/gridline-factory';

export class BarLineChart extends RootChart {
  private gridFactory: GridFactory;
  private gridlineFactory!: GridlineFactory;
  private lineFactory!: LineFactory;
  private dotFactory!: DotFactory;
  private barFactory!: BarFactory;

  private lineDataSetWithDataArray: DataSetWithDataArray[] = [];
  private barDataSetWithDataArray: DataSetWithDataArray[] = [];
  private lineAxisIndex: number = 0;
  private barAxisIndex: number = 0;
  private isUseGridlineFactory: boolean = true;
  private isUseBarFactory: boolean = true;
  private isUseLineFactory: boolean = true;
  private isUseDotFactory: boolean = true;

  constructor(selector: string) {
    super(selector);
    this.gridFactory = new GridFactory(this);
    this.gridFactory.setBandwidth(0.5);
    this.setFactory();
  }

  protected setFactory() {
    this.gridlineFactory = this.gridlineFactory
      ? this.gridlineFactory.setGridFactory(this.gridFactory)
      : new GridlineFactory(this.gridFactory);

    this.barFactory = this.barFactory
      ? this.barFactory.setGridFactory(this.gridFactory)
      : new BarFactory(this.gridFactory);
    this.barFactory.setDataSets(this.barDataSetWithDataArray, this.barAxisIndex);

    this.lineFactory = this.lineFactory
      ? this.lineFactory.setGridFactory(this.gridFactory)
      : new LineFactory(this.gridFactory);
    this.lineFactory.setDataSets(
      this.lineDataSetWithDataArray,
      this.lineAxisIndex
    );

    this.dotFactory = this.dotFactory
      ? this.dotFactory.setLineFactory(this.lineFactory)
      : new DotFactory(this.lineFactory);
  }

  /** 設定折線資料
   * @param dataSets DataSetWithDataArray[]
   * @param axisIndex ? 第幾條 key 軸，預設為 1
   */
  setLineDataSets(
    dataSets: DataSetWithDataArray[],
    axisIndex: number = 1
  ): BarLineChart {
    this.lineDataSetWithDataArray = dataSets;
    this.lineAxisIndex = axisIndex;
    this.setGridDataSets();
    return this;
  }

  /** 設定長條資料
   * @param dataSets DataSetWithDataArray[]
   * @param axisIndex ? 第幾條 key 軸，預設為 0
   */
  setBarDataSets(
    dataSets: DataSetWithDataArray[],
    axisIndex: number = 0
  ): BarLineChart {
    this.barDataSetWithDataArray = dataSets;
    this.barAxisIndex = axisIndex;
    this.setGridDataSets();
    return this;
  }

  private setGridDataSets() {
    const gridBarDataSetting = this.barDataSetWithDataArray.map((item) => {
      return {
        dataSetWithDataArray: item,
        isStack: true,
        axisIndex: this.barAxisIndex,
      };
    });
    const gridLineDataSetting = this.lineDataSetWithDataArray.map((item) => {
      return {
        dataSetWithDataArray: item,
        isStack: false,
        axisIndex: this.lineAxisIndex,
      };
    });
    this.gridFactory.setDataSetWithDataArrays([
      ...gridBarDataSetting,
      ...gridLineDataSetting,
    ]);
  }

  /** 設定要使用的 GridFactory */
  setGridFactory(gridFactory: GridFactory): BarLineChart {
    this.gridFactory = gridFactory;
    return this;
  }

  /** 設定要使用的 GridlineFactory */
  setGridlineFactory(gridlineFactory: GridlineFactory): BarLineChart {
    this.gridlineFactory = gridlineFactory;
    return this;
  }

  /** 設定要使用的 LineFactory */
  setLineFactory(lineFactory: LineFactory): BarLineChart {
    this.lineFactory = lineFactory;
    return this;
  }

  /** 設定要使用的 BarFactory */
  setBarFactory(barFactory: BarFactory): BarLineChart {
    this.barFactory = barFactory;
    return this;
  }

  getGridFactory(): GridFactory {
    return this.gridFactory;
  }

  getGridlineFactory(): GridlineFactory {
    return this.gridlineFactory;
  }

  getLineFactory(): LineFactory {
    return this.lineFactory;
  }

  getDotFactory(): DotFactory {
    return this.dotFactory;
  }

  getBarFactory(): BarFactory {
    return this.barFactory;
  }

  useGridlineFactory(isUseGridlineFactory: boolean): BarLineChart {
    this.isUseGridlineFactory = isUseGridlineFactory;
    return this;
  }

  useBarFactory(isUseBarFactory: boolean): BarLineChart {
    this.isUseBarFactory = isUseBarFactory;
    return this;
  }

  useLineFactory(isUseLineFactory: boolean): BarLineChart {
    this.isUseLineFactory = isUseLineFactory;
    return this;
  }

  useDotFactory(isUseDotFactory: boolean): BarLineChart {
    this.isUseDotFactory = isUseDotFactory;
    return this;
  }

  drawChart(useAnimation: boolean = false) {
    this.gridFactory.draw(useAnimation);
    this.setFactory();

    setTimeout(() => {
      this.isUseGridlineFactory
        ? this.gridlineFactory.draw(useAnimation)
        : this.gridlineFactory.clear(useAnimation);

      this.isUseBarFactory
        ? this.barFactory.draw(useAnimation)
        : this.barFactory.clear(useAnimation);

      this.isUseLineFactory
        ? this.lineFactory.draw(useAnimation)
        : this.lineFactory.clear(useAnimation);

      this.isUseDotFactory
        ? this.dotFactory.draw(useAnimation)
        : this.dotFactory.clear(useAnimation);
    }, 0);
  }
}
