import * as d3 from 'd3';
import { RootChart } from '../../chart/root-chart';
import { Color } from '../../config/color.config';
import { getColorTypeArray, getCssColor } from '../../function/color.function';
import { deepCopy, getRandomId } from '../../function/common.function';
import { DataSet, DataSetWithDataArray } from '../../interface/common.interface';
import { BasicFactory } from '../basic-factory';
import { DefaultLegendConfig, LegnedConfig } from './legend.config';

export class LegendFactory extends BasicFactory<LegendFactory> {
  private dataSets: DataSet[] = [];
  private legendGroup: d3.Selection<d3.BaseType, unknown, HTMLElement, any>;
  private legendConfig: LegnedConfig = deepCopy(DefaultLegendConfig);
  private legendId: string = getRandomId();
  private colorArray: Color[][] = [];

  constructor(private input: RootChart | string) {
    super();
    if (typeof this.input !== 'string') {
      this.legendGroup = this.input.svg.select('g.d3-legendGroup');
    } else {
      this.legendGroup = d3
        .select(this.input)
        .append('div')
        .attr('class', 'd3-legendGroup')
        .attr('id', `legendGroup_${this.legendId}`)
        .style('display', 'flex')
        .style('flex-direction', this.legendConfig['flex-direction'])
        .style('align-items', this.legendConfig['align-items'])
        .style('justify-content', this.legendConfig['justify-content'])
        .style('gap', this.legendConfig['group-gap'])
        .style('padding', this.legendConfig.padding) as any;
    }
  }

  setDataSets(dataSetsArray: DataSetWithDataArray[][]): LegendFactory {
    this.dataSets = dataSetsArray.flat();
    this.colorArray = [];
    for (const dataSets of dataSetsArray) {
      const color = getColorTypeArray(dataSets);
      const colorSubArray = color.slice(0, dataSets.length);
      this.colorArray.push(...colorSubArray);
    }
    return this;
  }

  setConfig(legendConfig: Partial<LegnedConfig>): LegendFactory {
    Object.assign(this.legendConfig, legendConfig);
    return this;
  }

  getSelector(): string {
    return `#legendGroup_${this.legendId}`;
  }

  getConfig(): LegnedConfig {
    return this.legendConfig;
  }

  draw(useAnimation: boolean = false): void {
    this.legendGroup
      .style('display', 'flex')
      .style('flex-direction', this.legendConfig['flex-direction'])
      .style('align-items', this.legendConfig['align-items'])
      .style('justify-content', this.legendConfig['justify-content'])
      .style('gap', this.legendConfig['group-gap'])
      .style('padding', this.legendConfig.padding) as any;

    const that = this;
    const appendIcon = function (selection: d3.Selection<any, any, any, any>) {
      selection.each(function (d: DataSet, i) {
        d3.select(this)
          .append('div')
          .attr('class', 'd3-legend-icon')
          .attr('id', `legend_icon_${that.legendId}_${i}`)
          .style('width', '20px')
          .style('height', '6px')
          .style('border-radius', '9999px')
          .style('background', getCssColor(that.colorArray[i]));
      });
    };

    const appendText = function (selection: d3.Selection<any, any, any, any>) {
      selection.each(function (d: DataSet, i: number) {
        d3.select(this)
          .append('span')
          .attr('class', 'd3-legend-text')
          .attr('id', `legend_text_${that.legendId}_${i}`)
          .style('font-size', that.legendConfig['font-size'])
          .style('font-weight', that.legendConfig['font-weight'])
          .style('font-family', that.legendConfig['font-family'])
          .style('color', that.legendConfig['text-color'])
          .style('user-select', 'none')
          .text(d.label);
      });
    };

    this.legendGroup
      .selectAll('div.d3-legend')
      .data(this.dataSets)
      .join(
        enter =>
          enter
            .append('div')
            .attr('class', 'd3-legend')
            .attr('id', (d, i) => `legend_${this.legendId}_${i}`)
            .style('display', 'flex')
            .style('align-items', 'center')
            .style('gap', '4px')
            .call(appendIcon)
            .call(appendText),
        update =>
          update
            .call(selection => selection.select('.d3-legend-icon').remove()) // Remove old icon
            .call(selection => selection.select('.d3-legend-text').remove()) // Remove old text
            .call(appendIcon) // Re-append updated icon
            .call(appendText), // Re-append updated text,
        exit =>
          exit
            .transition()
            .duration(useAnimation ? 500 : 0)
            .style('opacity', 0)
            .remove()
      );

    this.isFirstUpdate = false;
  }

  clear(): void {
    this.legendGroup.selectAll().transition().duration(500).style('opacity', 0).remove();
  }
}
