import { Color } from '../../config/color.config';
import { deepCopy } from '../../function/common.function';
import { BasicFactory } from '../basic-factory';
import { PositionStrategy } from './position-strategy.function';
import { DefaultTooltipConfig, TooltipConfig } from './tooltip.config';

export abstract class TooltipFactory<T> extends BasicFactory<
  TooltipFactory<T>
> {
  protected tooltip!: d3.Selection<HTMLDivElement, unknown, HTMLElement, any>;
  protected tooltipConfig: TooltipConfig = deepCopy(DefaultTooltipConfig);

  abstract setData(d: any): T;

  /** 設定 tooltip 標題 */
  setTitle(title: string): T {
    this.tooltipConfig.title = title;
    return this as unknown as T;
  }

  /** 設定顯示要黏住哪個元素 */
  setElement(element: Element): T {
    this.tooltipConfig.element = element;
    this.tooltipConfig.rect = element.getBoundingClientRect();
    return this as unknown as T;
  }

  /** 設定資料顯示顏色陣列 */
  setColor(colorsArray: Color[][]): T {
    this.tooltipConfig.colorsArray = colorsArray;
    return this as unknown as T;
  }

  /** 設定顯示策略 */
  setPositionStrategy(positionStrategy: PositionStrategy): T {
    this.tooltipConfig.positionStrategy = positionStrategy;
    return this as unknown as T;
  }

  clear(): void {
    this.tooltip.remove();
  }
}
