import * as d3 from 'd3';
import { Data } from '../../interface/common.interface';
import { TooltipFactory } from './tooltip-factory';
import { calculatePosition } from './position-strategy.function';
import { getCssColor } from '../../function/color.function';
import { TooltipConfig } from './tooltip.config';
import { Direction } from '../../enum/enum';

export class BasicTooltipFactory extends TooltipFactory<BasicTooltipFactory> {
  setData(d: Data[]): BasicTooltipFactory {
    this.tooltipConfig.data = d;
    return this;
  }

  setConfig(tooltipConfig: Partial<TooltipConfig>): TooltipFactory<BasicTooltipFactory> {
    Object.assign(this.tooltipConfig, tooltipConfig);
    return this;
  }

  getSelector(): string {
    return ``;
  }

  getConfig(): TooltipConfig {
    return this.tooltipConfig;
  }

  draw(): void {
    const that = this;
    this.tooltip = d3
      .select('body')
      .append('div')
      .attr('class', 'd3-tooltip')
      .style('display', 'flex')
      .style('flex-direction', 'column')
      .style('gap', '4px')
      .style('background', '#686868')
      .style('width', 'fit-content')
      .style('padding', '8px')
      .style('border-radius', '4px')
      .style('color', '#bebebe')
      .style('position', 'absolute')
      .style('top', `0px`)
      .style('left', `0px`)
      .style('pointer-events', 'none')
      .style('z-index', 9999)
      .style('opacity', 0)
      .call((selection) =>
        selection.transition().duration(300).style('opacity', 1)
      );

    for (const [i, data] of that.tooltipConfig.data.entries()) {
      const content = this.tooltip
        .append('div')
        .attr('class', 'd3-content')
        .style('display', 'flex')
        .style('align-items', 'center')
        .style('gap', '8px');

      content
        .append('div')
        .attr('class', 'd3-data-color')
        .style('width', '6px')
        .style('height', '18px')
        .style('border-radius', '9999px')
        .style(
          'background',
          getCssColor(that.tooltipConfig.colorsArray[i], Direction.vertical)
        );

      const text = content
        .append('span')
        .attr('class', 'd3-data-text')
        .style('font-size', '14px')
        .style('user-select', 'none')
        .text(`${data.key}: ${data.value}`);

      if (that.tooltipConfig.activeIndex == i) {
        text.style('color', 'white');
      }
    }

    const position = calculatePosition(
      that.tooltipConfig.element,
      this.tooltip.node(),
      that.tooltipConfig.positionStrategy
    );
    this.tooltip
      .style('top', 0)
      .style('left', 0)
      .style('transform', `translate(${position.x}px, ${position.y}px)`);
  }
}
