import * as d3 from 'd3';
import { Observable, Subject } from 'rxjs';
import { RootSvg } from '../../../chart/root-svg';
import {
  createText,
  deepCopy,
  getTextRect,
} from '../../../function/common.function';
import { DataSetWithData } from '../../../interface/common.interface';
import { BasicFactory } from '../../basic-factory';
import { DefaultRadialConfig, LabelDispalyMode, RadialConfig } from './radial.config';
import { DefaultRadialState, RadialState } from './radial-state';

/** 輻射定位組件，輻射中心型圖表必須使用 */
export class RadialFactory extends BasicFactory<RadialFactory> {
  private radialGroup: d3.Selection<any, unknown, HTMLElement, any>;
  private dataSetWithData: DataSetWithData[] = [];
  private radialConfig: RadialConfig = deepCopy(DefaultRadialConfig);
  private radialState: RadialState = deepCopy(DefaultRadialState);
  private pointerover$: Subject<any> = new Subject();
  private pointerout$: Subject<any> = new Subject();

  constructor(private chart: RootSvg) {
    super();
    this.radialGroup = this.chart.svg
      .select(`g#radialFactory_${this.factoryId}`)
      .node()
      ? this.chart.svg.select(`g#radialFactory_${this.chart.id}`)
      : this.chart.svg
          .append('g')
          .attr('id', `radialFactory_${this.factoryId}`);

    this.radialGroup.attr(
      'transform',
      `translate(${this.chart.size.width / 2},${this.chart.size.height / 2 + 5})` // 暫時補正5點，之後調整
    );
  }

  setConfig(radialConfig: Partial<RadialConfig>): RadialFactory {
    Object.assign(this.radialConfig, radialConfig);
    return this;
  }

  setData(data: DataSetWithData[]): RadialFactory {
    this.dataSetWithData = data;
    return this;
  }

  getConfig(): RadialConfig {
    return this.radialConfig;
  }

  getState(): RadialState {
    return this.radialState;
  }

  getSelector(): string {
    return `g#radialFactory_${this.factoryId}`;
  }

  /** 監聽 Pointerover 事件 */
  onPointerover(): Observable<PointerEvent> {
    return this.pointerover$.asObservable();
  }

  /** 監聽 Pointerout 事件 */
  onPointerout(): Observable<PointerEvent> {
    return this.pointerout$.asObservable();
  }

  draw(useAnimation: boolean = false): void {
    // Sample data
    const fontSize = this.radialConfig.labelFontConfig['font-size'];
    const textHeight = getTextRect('q0p', undefined, fontSize).height / 0.8; // 文字高度
    let maxWidthItem = null;
    let maxWidth = 0;

    for (const item of this.dataSetWithData) {
      const text = (item.data.value || '').toString() + item.unitText || '';
      const { width } = getTextRect(text, undefined, fontSize); // 只取寬度

      if (width > maxWidth) {
        maxWidth = width;
        maxWidthItem = item;
      }
    }

    // Define the radius of the pie chart
    this.radialState.radius = Math.min(
      this.chart.size.width / 2 - maxWidth - this.radialConfig.labelOffset,
      this.chart.size.height / 2 - textHeight - this.radialConfig.labelOffset
    );
    this.radialState.labelSize = { height: textHeight, width: maxWidth };

    this.isFirstUpdate = false;
  }

  clear(useAnimation: boolean = false): void {}
}
