import { BarFactory } from '../../factory/grid/bar/bar-factory/bar-factory';
import { GridFactory } from '../../factory/grid/grid/grid-factory';
import { GridlineFactory } from '../../factory/grid/gridline/gridline-factory';
import { DataSetWithDataArray } from '../../interface/common.interface';
import { RootChart } from '../root-chart';

/** 基礎長條圖 */
export class BarChart extends RootChart {
  private gridFactory: GridFactory;
  private gridlineFactory!: GridlineFactory;
  private barFactory!: BarFactory;
  private barDataSetWithDataArray: DataSetWithDataArray[] = [];
  private isUseGridlineFactory: boolean = true;
  private isUseBarFactory: boolean = true;

  constructor(selector: string) {
    super(selector);
    this.gridFactory = new GridFactory(this);
    this.gridFactory.setBandwidth(0.5);
    this.setFactory();
  }

  protected setFactory(): void {
    this.gridlineFactory = this.gridlineFactory
      ? this.gridlineFactory.setGridFactory(this.gridFactory)
      : new GridlineFactory(this.gridFactory);

    this.barFactory = this.barFactory
      ? this.barFactory.setGridFactory(this.gridFactory)
      : new BarFactory(this.gridFactory);
  }

  /** 設定資料 */
  setDataSets(dataSets: DataSetWithDataArray[], isStack: boolean = false): BarChart {
    this.barDataSetWithDataArray = dataSets;
    const gridDataSetting = this.barDataSetWithDataArray.map((item) => {
      return { dataSetWithDataArray: item, isStack: isStack };
    });
    this.gridFactory.setDataSetWithDataArrays(gridDataSetting);
    this.barFactory.setDataSets(this.barDataSetWithDataArray);
    this.barFactory.setStack(isStack);
    return this;
  }

  /** 設定要使用的 GridFactory */
  setGridFactory(gridFactory: GridFactory): BarChart {
    this.gridFactory = gridFactory;
    return this;
  }

  /** 設定要使用的 GridlineFactory */
  setGridlineFactory(gridlineFactory: GridlineFactory): BarChart {
    this.gridlineFactory = gridlineFactory;
    return this;
  }

  /** 設定要使用的 BarFactory */
  setBarFactory(barFactory: BarFactory): BarChart {
    this.barFactory = barFactory;
    return this;
  }

  getGridFactory(): GridFactory {
    return this.gridFactory;
  }

  getGridlineFactory(): GridlineFactory {
    return this.gridlineFactory;
  }

  getBarFactory(): BarFactory {
    return this.barFactory;
  }

  drawChart(useAnimation: boolean = false) {
    this.gridFactory.draw(useAnimation);
    this.setFactory();

    setTimeout(() => {
      this.isUseGridlineFactory
        ? this.gridlineFactory.draw(useAnimation)
        : this.gridlineFactory.clear(useAnimation);

      this.isUseBarFactory
        ? this.barFactory.draw(useAnimation)
        : this.barFactory.clear(useAnimation);
    }, 0);
  }
}
