import { ColorType } from '../enum/color.enum';

export enum FontFamilyType {
  Common = '"PingFang TC","Segoe UI", "Roboto", "微軟正黑體", "sans-serif"',
}

export type FontWeightType =
  | '100'
  | '200'
  | '300'
  | '400'
  | '500'
  | '600'
  | '700'
  | '800'
  | '900';


/**
 * 文字相關設定
 * @param font-size string
 * @param font-family string
 * @param font-weight '100' ~ '900'
 * @param color string
 */
export interface FontConfig {
  'font-size': string;
  'font-family': string;
  'font-weight': FontWeightType;
  color: string;
}

/** 標籤文字預設樣式 */
export const DefaultLabel: FontConfig = {
  'font-size': '12px',
  'font-family': FontFamilyType.Common,
  'font-weight': '400',
  color: ColorType.AxisLabel,
};

/** 軸線文字預設樣式 */
export const DefaultAxisLabel: FontConfig = {
  'font-size': '14px',
  'font-family': FontFamilyType.Common,
  'font-weight': '400',
  color: ColorType.AxisLabel,
};

