import * as d3 from 'd3';

/** 取得字串長寬 */
export function getTextRect(
  text: string,
  fontFamily: string = '',
  fontSize: string = '16',
  fontWeight: string = '400'
) {
  const tempSvg = d3.select('body').append('svg');

  const tempSpan = tempSvg
    .append('text')
    .attr('visibility', 'hidden')
    .attr('position', 'absolute')
    .attr('left', 0)
    .attr('font-family', fontFamily)
    .attr('font-size', fontSize)
    .attr('font-weight', fontWeight)
    .text(text);

  const rect = tempSpan.node()?.getBoundingClientRect() || new DOMRect();

  tempSvg.remove();

  return rect;
}

/** 深拷貝 */
export function deepCopy(obj: any) {
  return JSON.parse(JSON.stringify(obj));
}

/** 生成隨機小寫英文字串，預設 8 碼 */
export function getRandomId(length: number = 8): string {
  let id = '';
  const randomChar = 'abcdefghijklmnopqrstuvwxyz';
  for (let i = 0; i < length; i++) {
    const randomBuffer: Uint32Array = new Uint32Array(1);
    window.crypto.getRandomValues(randomBuffer);
    id += randomChar.charAt(Math.floor((randomBuffer[0] / (0xffffffff + 1)) * randomChar.length));
  }

  return id;
}

