export function truncateText(textElement: d3.Selection<any, unknown, null, undefined>, maxWidth: number): void {
  let text = textElement.text();

  // 如果文本寬度超過 maxWidth，進行截斷
  while (textElement.node()?.getComputedTextLength()! > maxWidth && text.length > 0) {
    text = text.slice(0, -1); // 逐字縮短文本
    textElement.text(text + '...'); // 在文本末尾加上 "..."
  }
}

/** 生成 text 標籤
 *
 * 預設數值:
 * - dominant-baseline: middle;
 * - pointer-events: none;
 * - user-select: none; */
export function createText(
  parent: d3.Selection<any, any, any, any>,
  customAttrs: (text: d3.Selection<any, any, any, any>) => void
): d3.Selection<any, unknown, any, any> {
  return parent
    .append('text')
    .attr('dominant-baseline', 'middle')
    .style('pointer-events', 'none')
    .style('user-select', 'none')
    .call(customAttrs);
}
