import * as d3 from 'd3';
import { Observable, Subject } from 'rxjs';
import { createText, deepCopy } from '../../../function/common.function';
import { DataSetWithData } from '../../../interface/common.interface';
import { BasicFactory } from '../../basic-factory';
import { BasicTooltipFactory } from '../../tooltip/basic-tooltip-factory';
import { TooltipFactory } from '../../tooltip/tooltip-factory';
import { DefaultPieConfig, PieConfig } from './pie.config';
import { RadialFactory } from '../radial/radial-factory';
import { RadialState } from '../radial/radial-state';
import { LabelDispalyMode, RadialConfig } from '../radial/radial.config';

/** 雷達組件 */
export class PieFactory extends BasicFactory<PieFactory> {
  private pieGroup: d3.Selection<any, unknown, HTMLElement, any>;
  private radialState: RadialState;
  private radialConfig: RadialConfig;
  private dataSetWithData: DataSetWithData[] = [];
  private pieConfig: PieConfig = deepCopy(DefaultPieConfig);
  private pointerover$: Subject<any> = new Subject();
  private pointerout$: Subject<any> = new Subject();

  constructor(
    radialFactory: RadialFactory,
    private tooltipFactory: TooltipFactory<any> = new BasicTooltipFactory()
  ) {
    super();
    this.radialState = radialFactory.getState();
    this.radialConfig = radialFactory.getConfig();
    this.pieGroup = d3.select(`g#pieFactory_${this.factoryId}`).node()
      ? d3.select(`g#pieFactory_${this.factoryId}`)
      : d3
          .select(radialFactory.getSelector())
          .append('g')
          .attr('id', `pieFactory_${this.factoryId}`);
    this.tooltipFactory.setPositionStrategy({ x: 'right', y: 'center' });
  }

  setConfig(pieConfig: Partial<PieConfig>): PieFactory {
    Object.assign(this.pieConfig, pieConfig);
    return this;
  }

  setRadialFactory(radialFactory: RadialFactory): PieFactory {
    this.radialState = radialFactory.getState();
    this.radialConfig = radialFactory.getConfig();
    return this;
  }

  setData(data: DataSetWithData[]): PieFactory {
    this.dataSetWithData = data;
    return this;
  }

  getConfig(): PieConfig {
    return this.pieConfig;
  }

  getSelector(): string {
    return `g#pieFactory_${this.factoryId}`;
  }

  /** 監聽 Pointerover 事件 */
  onPointerover(): Observable<PointerEvent> {
    return this.pointerover$.asObservable();
  }

  /** 監聽 Pointerout 事件 */
  onPointerout(): Observable<PointerEvent> {
    return this.pointerout$.asObservable();
  }

  draw(useAnimation: boolean = false): void {
    const pie = d3.pie<number>().sort(null);
    const ratio = Math.min(Math.max(this.pieConfig.donutWidthRatio ?? 1, 0), 1);
    const arc = d3
      .arc<d3.PieArcDatum<number>>()
      .outerRadius(this.radialState.radius)
      .innerRadius(this.radialState.radius * (1 - ratio));

    const labelArc = d3
      .arc<d3.PieArcDatum<number>>()
      .outerRadius(this.radialState.radius)
      .innerRadius(this.radialState.radius);

    const offset = this.radialConfig.labelOffset;
    const data = this.dataSetWithData.map((d) => Number(d.data.value));
    const pieData = pie(data);

    // ----------- Pie slices -------------
    const slices = this.pieGroup
      .selectAll<SVGPathElement, d3.PieArcDatum<number>>('path.pie-slice')
      .data(pieData);

    slices
      .join(
        (enter) =>
          enter
            .append('path')
            .attr('class', 'pie-slice')
            .attr('d', arc)
            .attr('fill', (d, i) => {
              const colors = this.dataSetWithData?.[i]?.colors;
              return colors?.[0] ?? d3.schemeCategory10[i % 10];
            })
            .style('opacity', 0),
        (update) => update,
        (exit) => exit.transition().duration(300).style('opacity', 0).remove()
      )
      .transition()
      .duration(useAnimation ? 500 : 0)
      .attr('d', arc)
      .style('opacity', 1);

    // ----------- Labels -------------
    const labels = this.pieGroup
      .selectAll<SVGTextElement, d3.PieArcDatum<number>>('text.label')
      .data(pieData);

    labels
      .join(
        (enter) =>
          enter.append('text').attr('class', 'label').style('opacity', 0),
        (update) => update,
        (exit) => exit.transition().duration(300).style('opacity', 0).remove()
      )
      .text((d, i) => {
        const value = d.data.toString();
        const prefix =
          this.radialConfig.labelDispalyMode === LabelDispalyMode.LabelValue
            ? this.dataSetWithData[i].label + ':'
            : '';
        return `${prefix} ${value} ${this.dataSetWithData[i].unitText || ''}`;
      })
      .attr('transform', (d) => `translate(${labelArc.centroid(d)})`)
      .attr('dx', (d) => {
        const angleRad = (d.startAngle + d.endAngle) / 2;
        return Math.cos(angleRad - Math.PI / 2) * offset;
      })
      .attr('dy', (d) => {
        const angleRad = (d.startAngle + d.endAngle) / 2;
        const adjustedSin = Math.sin(angleRad - Math.PI / 2);
        return (
          adjustedSin * (offset + this.radialState.labelSize.height / 2) +
          this.radialState.labelSize.height / 12
        );
      })
      .style('text-anchor', (d) => {
        const angleDeg = ((d.startAngle + d.endAngle) / 2) * (180 / Math.PI);
        if (
          (angleDeg >= -5 && angleDeg <= 5) ||
          (angleDeg >= 175 && angleDeg <= 185)
        ) {
          return 'middle';
        }
        return angleDeg < 180 ? 'start' : 'end';
      })
      .style('font-size', this.radialConfig.labelFontConfig['font-size'])
      .style('font-family', this.radialConfig.labelFontConfig['font-family'])
      .style('font-weight', this.radialConfig.labelFontConfig['font-weight'])
      .style('fill', this.radialConfig.labelFontConfig['color'])
      .transition()
      .duration(useAnimation ? 500 : 0)
      .style('opacity', 1);

    this.isFirstUpdate = false;
  }

  clear(useAnimation: boolean = false): void {
    this.pieGroup
      .selectAll('path.pie-slice')
      .transition()
      .duration(useAnimation ? 500 : 0)
      .style('opacity', 0)
      .remove();

    this.pieGroup
      .selectAll('text.label')
      .transition()
      .duration(useAnimation ? 500 : 0)
      .style('opacity', 0)
      .remove();
  }
}
