import * as d3 from 'd3';
import { RootSvg } from './../../../chart/root-svg';
import { deepCopy, getRandomId, getTextRect } from '../../../function/common.function';
import { FontConfig } from '../../../config/font.config';
import {
  DataSetWithDataArray,
  MinMaxValue,
  Size,
} from '../../../interface/common.interface';
import { GridState } from './grid.state';
import { Direction } from '../../../enum/enum';
import { LayoutPosition } from '../../../interface/position.interface';
import { Color } from '../../../config/color.config';
import { getColorTypeArray } from '../../../function/color.function';
import { Observable, Subject } from 'rxjs';
import { ColorType } from '../../../enum/color.enum';
import { DefaultGridConfig, GridConfig, KeyGridlineAlignment } from './grid.config';
import { BasicFactory } from '../../basic-factory';

/** 格線的游標事件，onPointerover 與 onClick 回傳的型別 */
export interface GridPointerEvent {
  /** key 軸索引值 */
  keyIndex: number;
  /** 事件回傳的數據 */
  data: DataSetWithDataArray[];
  /** 離游標最近的數據索引值 */
  nearestDataIndex: number;
  /** 游標事件 */
  event: PointerEvent;
  /** hover 或 click 事件的 HTMLElement */
  element: HTMLElement;
}

/** 格線定位組件，格線型圖表必須使用 */
export class GridFactory extends BasicFactory<GridFactory> {
  private gridGroup: d3.Selection<any, unknown, HTMLElement, any>;
  private gridState!: GridState;
  private gridConfig: GridConfig = deepCopy(DefaultGridConfig);
  private dataSetWithDataArrays: DataSetWithDataArray[] = [];

  // gridState 所有變數
  private x: d3.ScaleLinear<number, number, never> | d3.ScaleBand<string> =
    getScaleLinear(0, 0, 0, 0);
  private xTop: d3.ScaleLinear<number, number, never> | d3.ScaleBand<string> =
    getScaleLinear(0, 0, 0, 0);
  private xGridlineBottom:
    | d3.ScaleLinear<number, number, never>
    | d3.ScaleBand<string> = getScaleLinear(0, 0, 0, 0);
  private xGridlineTop:
    | d3.ScaleLinear<number, number, never>
    | d3.ScaleBand<string> = getScaleLinear(0, 0, 0, 0);
  private y: d3.ScaleLinear<number, number, never> | d3.ScaleBand<string> =
    getScaleLinear(0, 0, 0, 0);
  private yRight: d3.ScaleLinear<number, number, never> | d3.ScaleBand<string> =
    getScaleLinear(0, 0, 0, 0);
  private yGridlineLeft:
    | d3.ScaleLinear<number, number, never>
    | d3.ScaleBand<string> = getScaleLinear(0, 0, 0, 0);
  private yGridlineRight:
    | d3.ScaleLinear<number, number, never>
    | d3.ScaleBand<string> = getScaleLinear(0, 0, 0, 0);
  private xGroup: d3.Selection<any, unknown, HTMLElement, any>;
  private xTopGroup: d3.Selection<any, unknown, HTMLElement, any>;
  private yGroup: d3.Selection<any, unknown, HTMLElement, any>;
  private yRightGroup: d3.Selection<any, unknown, HTMLElement, any>;
  private gridSize: Size = { width: 0, height: 0 };
  private gridMargin: LayoutPosition = { top: 0, bottom: 0, left: 0, right: 0 };
  private axisValues: MinMaxValue[] = [{ minValue: 0, maxValue: 0 }];
  private keyScaleLabels: string[] = [];
  private keyScaleStep: number = 0;
  private isVerticalLabel: boolean = false;
  private labelRotateAngle: number = Math.PI / 3; // 60度
  private xTextRectArray: DOMRect[] = [];

  // grid factory 私有變數
  /** 上一筆資料集，若資料集為空則使用這個 */
  private lastDataSetsWithDataArray: DataSetWithDataArray[] = [
    {
      label: '',
      colors: [],
      data: [{ key: '', value: 0 }],
    },
  ];
  private gridId: string = getRandomId(8);
  private pointerover$: Subject<GridPointerEvent> = new Subject();
  private pointerout$: Subject<PointerEvent> = new Subject();
  private click$: Subject<GridPointerEvent> = new Subject();

  constructor(private chart: RootSvg) {
    super();
    this.gridGroup = this.chart.svg.select(`g#gridFactory_${this.chart.id}_${this.gridId}`).node()
      ? this.chart.svg.select(`g#gridFactory_${this.chart.id}`)
      : this.chart.svg.append('g').attr('id', `gridFactory_${this.chart.id}_${this.gridId}`);
    this.xGroup = this.gridGroup.select('g#x').node()
      ? this.gridGroup.select('g#x')
      : this.gridGroup.append('g').attr('id', 'x');
    this.xTopGroup = this.gridGroup.select('g#x_top').node()
      ? this.gridGroup.select('g#x_top')
      : this.gridGroup.append('g').attr('id', 'x_top');
    this.yGroup = this.gridGroup.select('g#y').node()
      ? this.gridGroup.select('g#y')
      : this.gridGroup.append('g').attr('id', 'y');
    this.yRightGroup = this.gridGroup.select('g#y_right').node()
      ? this.gridGroup.select('g#y_right')
      : this.gridGroup.append('g').attr('id', 'y_right');
  }

  setConfig(config: Partial<GridConfig>): GridFactory {
    Object.assign(this.gridConfig, config);
    return this;
  }

  /** 設定資料，分為堆疊型與非堆疊型 */
  setDataSetWithDataArrays(
    dataSetWithDataArraysDetail: {
      dataSetWithDataArray: DataSetWithDataArray;
      isStack: boolean;
      axisIndex?: number;
    }[]
  ): GridFactory {
    this.dataSetWithDataArrays = dataSetWithDataArraysDetail.map(
      (item) => item.dataSetWithDataArray
    );
    this.calculateMinMax(dataSetWithDataArraysDetail);
    return this;
  }

  /** 設定 value 軸的刻度等分數量，ex. 數值為 0 ~ 100，5 等分則每個刻度 20 */
  setStepsCount(stepsCount: number): GridFactory {
    this.gridConfig.stepsCount = stepsCount;
    return this;
  }

  /** 設定 x 軸標籤文字樣式 */
  setXLabelFont(fontConfig: Partial<FontConfig>): GridFactory {
    Object.assign(this.gridConfig.xlabelFontConfig, fontConfig);
    return this;
  }

  /** 設定 y 軸標籤文字樣式 */
  setYLabelFont(fontConfig: Partial<FontConfig>): GridFactory {
    Object.assign(this.gridConfig.ylabelFontConfig, fontConfig);
    return this;
  }

  /** 設定 x 軸標籤單位文字 */
  setKeyLabelUnit(unit: string): GridFactory {
    this.gridConfig.keyUnit = unit;
    return this;
  }

  /** 設定 y 軸標籤單位文字 */
  setValueLabelUnit(unit: string): GridFactory {
    this.gridConfig.valueUnit = unit;
    return this;
  }

  /** 設定圖表呈現方向，預設為 vertical (key 在 x 軸，value 在 y 軸) */
  setDirection(direction: Direction): GridFactory {
    this.gridConfig.direction = direction;
    return this;
  }

  /** 設定 key 軸格線繪製的位置
   * - `KeyGridlineAlignment.center` 格線畫在刻度中心
   * - `KeyGridlineAlignment.centerPadding` 格線畫在刻度中心，且兩邊多空出一格寬度
   * - `KeyGridlineAlignment.between` 格線畫在刻度兩邊
   */
  setKeyGridlineAlignment(keyGridlineAlignment: KeyGridlineAlignment): GridFactory {
    this.gridConfig.keyGridlineAlignment = keyGridlineAlignment;
    return this;
  }

  /** 設定 key 軸頻帶寬度，數值介於 0 ~ 1 */
  setBandwidth(bandwidth: number): GridFactory {
    this.gridConfig.bandwidth = Math.max(0, Math.min(1, bandwidth));
    return this;
  }

  /** 設定是否顯示 hover 的區域，需啟用 hover 功能 */
  setShowHoverArea(isShow: boolean): GridFactory {
    this.gridConfig.showHoverArea = isShow;
    return this;
  }

  /** 設定是否顯示點擊後的選取區域，需啟用 hover 功能 */
  setShowActiveArea(isShow: boolean): GridFactory {
    this.gridConfig.showActiveArea = isShow;
    return this;
  }

  /** 設定是否啟用 hover 功能 */
  setHover(isUsePointerEvent: boolean): GridFactory {
    this.gridConfig.isUsePointerEvent = isUsePointerEvent;
    return this;
  }

  /** 設定 hover 區域的顏色 */
  setHoverColor(hoverColor: string): GridFactory {
    this.gridConfig.hoverColor = hoverColor;
    return this;
  }

  /** 設定 hover 區域的寬度，預設為 -1 (-1代表不設定，寬度為一格 key軸 刻度) */
  setHoverStrokeWidth(strokeWidth: number): GridFactory {
    this.gridConfig.hoverStrokeWidth = strokeWidth;
    return this;
  }

  /** 設定起始顯示點擊區間的索引值 */
  setActiveIndex(activeIndex: number): GridFactory {
    this.gridConfig.activeIndex = activeIndex;
    return this;
  }

  /** 設定點擊後的選取區域的顏色 */
  setActiveColor(activeColor: string): GridFactory {
    this.gridConfig.activeColor = activeColor;
    return this;
  }

  /** 設定點擊後的選取區域對應的 key 軸標籤文字顏色 */
  setActiveTextColor(activeTextColor: string): GridFactory {
    this.gridConfig.activeTextColor = activeTextColor;
    return this;
  }

  /** 設定格線的最小數值(若最小值大於數據中的最小值，圖表會無視此項設定，自動計算該顯示的最小值)
   * @param min 最小值
   * @param axisIndex ? 第幾條 key 軸，預設為 0
   */
  setMinValue(min: number, axisIndex: number = 0): GridFactory {
    this.gridConfig.settingMin[axisIndex == 0 ? 0 : 1] = min;
    return this;
  }

  /** 設定格線的最大數值(若最大值小於數據中的最大值，圖表會無視此項設定，自動計算該顯示的最大值)
   * @param max 最大值
   * @param axisIndex ? 第幾條 key 軸，預設為 0
   */
  setMaxValue(min: number, axisIndex: number = 0): GridFactory {
    this.gridConfig.settingMax[axisIndex == 0 ? 0 : 1] = min;
    return this;
  }

  /** 設定格線與標籤的間距 */
  setGridGap(gridGap: Partial<LayoutPosition>): GridFactory {
    Object.assign(this.gridConfig.gridGap, gridGap);
    return this;
  }

  /** 設定顯示軸線標籤，(影響格線內推空間) */
  setShowAxisLabel(isShow: boolean): GridFactory {
    this.gridConfig.isShowAxisLabel = isShow;
    return this;
  }

  getConfig(): GridConfig {
    return deepCopy(this.gridConfig);
  }

  getSelector(): string {
    return `gridFactory_${this.factoryId}`;
  }

  /** 取得 GridFactory 中不可被設定的值 */
  getState(): GridState {
    this.gridState = {
      id: this.chart.id,
      x: this.x,
      xTop: this.xTop,
      xGridlineBottom: this.xGridlineBottom,
      xGridlineTop: this.xGridlineTop,
      y: this.y,
      yRight: this.yRight,
      yGridlineLeft: this.yGridlineLeft,
      yGridlineRight: this.yGridlineRight,
      gridGroup: this.gridGroup,
      xGroup: this.xGroup,
      xTopGroup: this.xTopGroup,
      yGroup: this.yGroup,
      yRightGroup: this.yRightGroup,
      gridSize: this.gridSize,
      gridMargin: this.gridMargin,
      axisValues: this.axisValues,
      keyScaleLabels: this.keyScaleLabels,
      keyScaleStep: this.keyScaleStep,
      isVerticalLabel: this.isVerticalLabel,
      labelRotateAngle: this.labelRotateAngle,
      xTextRectArray: this.xTextRectArray,
    };
    return { ...this.gridState };
  }

  /** 監聽 Pointerover 事件 */
  onPointerover(): Observable<GridPointerEvent> {
    return this.pointerover$.asObservable();
  }

  /** 監聽 Pointerout 事件 */
  onPointerout(): Observable<PointerEvent> {
    return this.pointerout$.asObservable();
  }

  /** 監聽點擊選取區間的事件 */
  onClick(): Observable<GridPointerEvent> {
    return this.click$.asObservable();
  }

  /** pointerover 觸發的事件 */
  private pointeroverEvent(
    keyIndex: number,
    data: DataSetWithDataArray[],
    nearestDataIndex: number,
    event: PointerEvent
  ) {
    setTimeout(() => {
      const el = document.querySelector(
        `path#hover-area_${this.chart.id}`
      ) as HTMLElement;
      this.pointerover$.next({
        keyIndex: keyIndex,
        data: this.dataSetWithDataArrays.length > 0 ? data : [],
        nearestDataIndex: nearestDataIndex,
        event: event,
        element: el,
      });
    }, 0);
  }

  /** pointerout 觸發的事件 */
  private pointeroutEvent(event: PointerEvent) {
    this.pointerout$.next(event);
  }

  /** 格線中點擊選取區間的事件 */
  private clickEvent(
    keyIndex: number,
    data: DataSetWithDataArray[],
    nearestDataIndex: number,
    event: PointerEvent
  ) {
    this.gridConfig.activeIndex = keyIndex;
    setTimeout(() => {
      const el = document.querySelector(
        `path#active-area_${this.chart.id}`
      ) as HTMLElement;
      this.click$.next({
        keyIndex: keyIndex,
        data: this.dataSetWithDataArrays.length > 0 ? data : [],
        nearestDataIndex: nearestDataIndex,
        event: event,
        element: el,
      });
    }, 0);
  }

  /** 計算最大最小值 */
  private calculateMinMax(
    dataSetWithDataArraysDetail: {
      dataSetWithDataArray: DataSetWithDataArray;
      isStack: boolean;
      axisIndex?: number;
    }[]
  ) {
    if (dataSetWithDataArraysDetail.length < 1) return; // 空值不計算
    // 左右軸線最大最小值
    const axisValues: MinMaxValue[] = [
      {
        minValue: this.gridConfig.settingMin[0],
        maxValue: this.gridConfig.settingMax[0],
      },
    ];
    const stackDataSetWithDataArrays: DataSetWithDataArray[] = []; // 堆疊的所有資料集合
    let stackAxisIndex: number = 0; // 堆疊軸線所引值
    for (const DataSetWithDataArrayDetail of dataSetWithDataArraysDetail) {
      const axisIndex = DataSetWithDataArrayDetail.axisIndex || 0;
      if (axisIndex >= axisValues.length) {
        axisValues.length = axisIndex + 1;
        axisValues[axisIndex] = {
          minValue: this.gridConfig.settingMin[1],
          maxValue: this.gridConfig.settingMax[0],
        };
      }

      // 堆疊與非堆疊型分開計算
      if (DataSetWithDataArrayDetail.isStack) {
        stackAxisIndex = DataSetWithDataArrayDetail.axisIndex || 0;
        stackDataSetWithDataArrays.push(
          DataSetWithDataArrayDetail.dataSetWithDataArray
        );
      } else {
        const valueArray =
          DataSetWithDataArrayDetail.dataSetWithDataArray.data.map((item) =>
            Number(item.value)
          );

        const dataMinValue = Math.min(...valueArray);
        const dataMaxValue = Math.max(...valueArray);

        axisValues[axisIndex].minValue = Math.min(
          dataMinValue,
          axisValues[axisIndex].minValue
        );
        axisValues[axisIndex].maxValue = Math.max(
          dataMaxValue,
          axisValues[axisIndex].maxValue
        );
      }
    }

    // 計算堆疊最大最小值
    let stacksMaxPositive = 0;
    let stacksMaxNegetive = 0;
    if (stackDataSetWithDataArrays.length > 0) {
      const categories = stackDataSetWithDataArrays[0].data.map((d) => d.key);
      const stackData = categories.map((category, i) => {
        let obj: any = { name: category };
        stackDataSetWithDataArrays.forEach((d) => {
          obj[d.label] = d.data[i].value;
        });
        return obj;
      });
      for (const stackDataItem of stackData) {
        const keys = Object.keys(stackDataItem);
        let stackMaxPositive = 0;
        let stackMaxNegetive = 0;
        for (let i = 1; i < keys.length; i++) {
          const value = stackDataItem[keys[i]];
          if (value > 0) {
            stackMaxPositive += value;
          } else {
            stackMaxNegetive += value;
          }
        }
        stacksMaxPositive = Math.max(stacksMaxPositive, stackMaxPositive);
        stacksMaxNegetive = Math.min(stacksMaxNegetive, stackMaxNegetive);
      }
    }

    for (const [i, axisValue] of axisValues.entries()) {
      const minValue = Math.min(
        axisValue.minValue,
        stackAxisIndex == i ? stacksMaxNegetive : 0,
        this.gridConfig.settingMin[i]
      );
      const maxValue = Math.max(
        axisValue.maxValue,
        stackAxisIndex == i ? stacksMaxPositive : 0,
        this.gridConfig.settingMax[i]
      );

      const [modifiedMin, modifiedMax] = getPrettyMinMax(
        minValue,
        maxValue,
        this.gridConfig.stepsCount
      );
      this.axisValues[i] = { minValue: modifiedMin, maxValue: modifiedMax };
    }
  }

  private calcGridSize() {
    this.gridSize = {
      width:
        this.chart.size.width - this.gridMargin.left - this.gridMargin.right,
      height:
        this.chart.size.height - this.gridMargin.top - this.gridMargin.bottom,
    };
  }

  draw(useAnimation?: boolean) {
    const isPaddingOuter = this.gridConfig.keyGridlineAlignment == KeyGridlineAlignment.centerPadding;

    // 無資料或全部資料為空，則以上一筆資料設定格狀
    const dataSetWithDataArrays =
      this.dataSetWithDataArrays.length < 1
        ? this.lastDataSetsWithDataArray
        : this.dataSetWithDataArrays;

    const keySet: Set<any> = new Set();
    // 遍歷每個 dataset，收集所有的 key
    dataSetWithDataArrays.forEach((dataset) => {
      dataset.data.forEach((item) => {
        keySet.add(item.key);
      });
    });
    this.keyScaleLabels = Array.from(keySet);

    //#region 計算標籤文字長寬，x軸計算第一個與最後一個，y軸計算長度最長的
    /** 每個軸線的所有 tick */
    const axisValueTicks: string[][] = [];
    for (const axisValue of this.axisValues) {
      const valueRange = Math.abs(axisValue.maxValue - axisValue.minValue);
      const axisValueTick = d3.range(
        axisValue.minValue,
        axisValue.maxValue + 1,
        valueRange / this.gridConfig.stepsCount
      );
      // 加上千分位
      axisValueTicks.push(axisValueTick.map((item) => item.toLocaleString()));
    }

    // 顯示軸線標籤時，計算格線內推距離
    if (this.gridConfig.isShowAxisLabel) {
      const keyRectArray: DOMRect[] = [];
      for (const scaleBandKey of this.keyScaleLabels) {
        const keyRect = getTextRect(
          scaleBandKey + this.gridConfig.keyUnit,
          this.gridConfig.xlabelFontConfig['font-family'],
          this.gridConfig.xlabelFontConfig['font-size'],
          this.gridConfig.xlabelFontConfig['font-weight']
        );
        keyRectArray.push(keyRect);
      }

      /** 計算每個軸線標籤 Rect */
      const valueRectArray = axisValueTicks.map((axisitem) =>
        axisitem.map((pointItem) =>
          getTextRect(
            pointItem + this.gridConfig.valueUnit,
            this.gridConfig.ylabelFontConfig['font-family'],
            this.gridConfig.ylabelFontConfig['font-size'],
            this.gridConfig.ylabelFontConfig['font-weight']
          )
        )
      );

      const xTextRectArray =
        this.gridConfig.direction == Direction.vertical
          ? [...keyRectArray]
          : [...valueRectArray[0]];
      const xFirstTextRect = xTextRectArray[0];
      const xLastTextRect = xTextRectArray[xTextRectArray.length - 1];
      const xMaxTextRect =
        xTextRectArray.length > 0
          ? xTextRectArray.reduce((maxItem, item) => {
              return item.width > maxItem.width ? item : maxItem;
            }, xTextRectArray[0])
          : { width: 0, height: 0 };
      this.xTextRectArray = xTextRectArray;

      const yLeftTextRectArray =
        this.gridConfig.direction == Direction.vertical
          ? valueRectArray[0]
          : [...keyRectArray];
      const yLeftMaxTextRect = yLeftTextRectArray.reduce((maxItem, item) => {
        return item.width > maxItem.width ? item : maxItem;
      }, yLeftTextRectArray[0]) || { width: 0, height: 0 };

      // 垂直且有兩個以上的y軸
      const yRightMaxTextRect =
        this.axisValues.length > 1
          ? valueRectArray[1].reduce((maxItem, item) => {
              return item.width > maxItem.width ? item : maxItem;
            }, valueRectArray[1][0]) || { width: 0, height: 0 }
          : new DOMRect();

      this.gridMargin = {
        top: Math.max(this.gridConfig.gridGap.top, yLeftMaxTextRect.height / 2),
        bottom: Math.max(
          xFirstTextRect.height + this.gridConfig.gridGap.bottom,
          yLeftMaxTextRect.height / 2
        ),
        left:
          Math.max(
            yLeftMaxTextRect.width + this.gridConfig.gridGap.left,
            xFirstTextRect.width / 2
          ) + 2,
        right:
          Math.max(
            yRightMaxTextRect.width + this.gridConfig.gridGap.right,
            xLastTextRect.width / 2
          ) + 2,
      };
      this.calcGridSize();

      //#region 計算格狀定為step，若字串最大長度大於step則將x軸標籤轉成斜的
      const scaleStepCount = Math.max(
        this.keyScaleLabels.length -
          1 +
          (isPaddingOuter ? 2 : 0),
        1
      );
      const scaleStep = this.gridSize.width / scaleStepCount;
      this.isVerticalLabel = xMaxTextRect.width > scaleStep; //若字串最大長度大於step則將x軸標籤轉成斜的
      this.labelRotateAngle = Math.PI / 3; // 旋轉角度60度
      const oneLetterRotateWidth =
        xMaxTextRect.height * Math.cos(this.labelRotateAngle); // 一個字旋轉後的寬度
      const rotateHeight =
        (xMaxTextRect.width + xMaxTextRect.height) *
          Math.sin(this.labelRotateAngle) -
        xFirstTextRect.height / 2; // 旋轉後的高，xMaxTextRect.height 為單一字元寬度
      const rotateWidth = xMaxTextRect.width * Math.cos(this.labelRotateAngle); // 旋轉後的寬

      // 重新計算 gridMargin
      this.gridMargin.left =
        Math.max(
          yLeftMaxTextRect.width + this.gridConfig.gridGap.left,
          this.isVerticalLabel
            ? rotateWidth -
                (isPaddingOuter ? scaleStep : 0) -
                oneLetterRotateWidth
            : xFirstTextRect.width / 2 -
                (isPaddingOuter ? scaleStep : 0)
        ) + 2;
      this.gridMargin.right =
        Math.max(
          yRightMaxTextRect.width + this.gridConfig.gridGap.right,
          this.isVerticalLabel
            ? oneLetterRotateWidth -
                (isPaddingOuter ? scaleStep : 0)
            : xLastTextRect.width / 2 -
                (isPaddingOuter &&
                this.gridConfig.direction == Direction.vertical
                  ? scaleStep
                  : 0)
        ) + 2;
      this.gridMargin.bottom = Math.max(
        this.gridConfig.gridGap.bottom +
          (this.isVerticalLabel ? rotateHeight : xFirstTextRect.height),
        yLeftMaxTextRect.height / 2
      );
      //#endregion
    } else {
      this.gridMargin = {
        top: this.gridConfig.gridGap.top,
        bottom: this.gridConfig.gridGap.bottom + 2,
        left: this.gridConfig.gridGap.left,
        right: this.gridConfig.gridGap.right + 2,
      };
    }

    this.calcGridSize();

    this.gridGroup
      .transition()
      .duration(useAnimation ? 500 : 0)
      .attr(
        'transform',
        `translate(${this.gridMargin.left}, ${this.gridMargin.top})`
      );

    //#region x y scacle 設定
    const paddingOuter = (() => {switch(this.gridConfig.keyGridlineAlignment) {
      case KeyGridlineAlignment.between: return 0.5;
      case KeyGridlineAlignment.centerPadding: return 1;
      default: return 0;
    }})();
    const bandwidth = 1 - this.gridConfig.bandwidth;
    if (this.gridConfig.direction == Direction.vertical) {
      this.xGridlineBottom = getScaleBand(
        this.keyScaleLabels,
        0,
        this.gridSize.width,
        bandwidth,
        paddingOuter
      );
      this.y = getScaleLinear(
        this.axisValues[0].minValue,
        this.axisValues[0].maxValue,
        this.gridSize.height,
        0
      );
      this.yGridlineLeft = getScaleBand(
        axisValueTicks[0].map((item) => item.toString()),
        this.gridSize.height,
        0,
        1
      );

      // 右方 Y 軸
      if (this.axisValues.length > 1) {
        this.yRight = getScaleLinear(
          this.axisValues[1].minValue,
          this.axisValues[1].maxValue,
          this.gridSize.height,
          0
        );
        this.yGridlineRight = getScaleBand(
          axisValueTicks[1].map((item) => item.toString()),
          this.gridSize.height,
          0,
          1
        );
      }
    } else {
      this.x = getScaleLinear(
        this.axisValues[0].minValue,
        this.axisValues[0].maxValue,
        0,
        this.gridSize.width
      );
      this.xGridlineBottom = getScaleBand(
        axisValueTicks[0].map((item) => item.toString()),
        0,
        this.gridSize.width,
        1
      );
      this.yGridlineLeft = getScaleBand(
        this.keyScaleLabels,
        this.gridSize.height,
        0,
        bandwidth,
        paddingOuter
      );

      // 上方 X 軸
      if (this.axisValues.length > 1) {
        this.xTop = getScaleLinear(
          this.axisValues[1].minValue,
          this.axisValues[1].maxValue,
          0,
          this.gridSize.width
        );
        this.xGridlineTop = getScaleBand(
          axisValueTicks[1].map((item) => item.toString()),
          0,
          this.gridSize.width,
          1
        );
      }
    }

    this.keyScaleStep =
      this.gridConfig.direction == Direction.vertical
        ? (this.xGridlineBottom as any).step()
        : (this.yGridlineLeft as any).step();
    //#endregion

    this.getState();

    // hover effect
    if (this.gridConfig.isUsePointerEvent) {
      setTimeout(() => {
        hoverPosition(
          this.chart,
          this.gridState,
          this.gridConfig,
          this.lastDataSetsWithDataArray,
          getColorTypeArray(this.dataSetWithDataArrays),
          this.pointeroverEvent.bind(this),
          this.pointeroutEvent.bind(this),
          this.clickEvent.bind(this)
        );
      }, 0);
    } else {
      this.chart.svg
        .selectAll(`g#grid-hover-view_${this.gridState.id}_${this.gridId}`)
        .remove();
      this.chart.svg
        .selectAll(`g#grid-hover-effects_${this.gridState.id}_${this.gridId}`)
        .remove();
    }

    this.lastDataSetsWithDataArray = deepCopy(this.dataSetWithDataArrays);
  }

  clear(useAnimation?: boolean) {
    this.gridGroup
    .selectAll('path')
    .transition()
    .duration(useAnimation ? 500 : 0)
    .style('opacity', 0)
    .remove();
  }
}

/** 取得比例尺-線性刻度: 連續的數值 */
function getScaleLinear(
  domainS: number,
  domainE: number,
  rangeS: number,
  rangeE: number
): d3.ScaleLinear<number, number, never> {
  return d3.scaleLinear().domain([domainS, domainE]).range([rangeS, rangeE]);
}

/** 取得比例尺-非線性刻度: ex: 非連續性資料像是性別或人名或是任意的資料集*/
function getScaleBand(
  keys: string[],
  rangeS: number,
  rangeE: number,
  bandwidth: number,
  paddingOuter: number = 0
): d3.ScaleBand<string> {
  return d3
    .scaleBand()
    .domain(keys)
    .range([rangeS, rangeE])
    .padding(bandwidth)
    .paddingOuter(paddingOuter - (1 - bandwidth) / 2);
}

/** hover 顯示當前x位置的所有點 */
function hoverPosition(
  rootChart: RootSvg,
  gridState: GridState,
  gridConfig: GridConfig,
  data: DataSetWithDataArray[],
  colors: Color[][],
  hoverCallback: Function,
  pointeroutCallback: Function,
  clickCallback: Function
) {
  let lastIndex = -1;
  let lastPostion = -1; // 計算滑鼠移動
  let nearestIndex = -1; // 距離滑鼠最近的數據組索引值
  let activeIndex = gridConfig.activeIndex;
  const id = rootChart.id;
  const isVertical = gridConfig.direction == Direction.vertical;
  const step = isVertical
    ? (gridState.xGridlineBottom as any).step()
    : (gridState.yGridlineLeft as any).step();
  const dataLength = data[0].data.length;
  const isPaddingOuter = gridConfig.keyGridlineAlignment == KeyGridlineAlignment.centerPadding;

  //#region 元素生成
  rootChart.svg.selectAll(`g#grid-hover-effects_${id}`).remove();
  rootChart.svg.selectAll(`g#grid-hover-view_${id}`).remove();
  const hoverViewGroup = rootChart.svg
    .insert('g', `g#gridFactory_${id}`)
    .attr('id', `grid-hover-view_${id}`);

  const hoverEffectsGroup = rootChart.svg
    .append('g')
    .attr('id', `grid-hover-effects_${id}`);

  hoverViewGroup
    .append('path')
    .attr('id', `hover-area_${id}`)
    .attr('stroke', gridConfig.hoverColor)
    .style('opacity', '0');

  hoverViewGroup
    .append('path')
    .attr('id', `active-area_${id}`)
    .attr('stroke', gridConfig.activeColor)
    .style('opacity', '0');

  const pointOfData = hoverViewGroup
    .selectAll(`#point-of-data_${id}`)
    .data(data)
    .enter()
    .append('g')
    .attr('id', `point-of-data_${id}`);

  const selector = `path#active-area_${id}`;
  drawArea(activeIndex, step, false);
  d3.select(selector).style('opacity', 0);
  if (gridConfig.isUsePointerEvent && gridConfig.showActiveArea && activeIndex >= 0) {
    d3.select(selector).style('opacity', 1);
  }

  if (gridConfig.showHoverPoint) {
    pointOfData
      .append('circle')
      .datum((d: any) => d)
      .attr('r', 7)
      .attr('stroke', 'white')
      .attr('stroke-width', 2)
      .style('fill', (d: any, i: number) => {
        return colors[i][0].color;
      })
      .style('opacity', '0');
  }
  //#endregion

  //#region 共用方法
  /** 滑鼠事件取得當前 key 軸 index */
  function getIndex(event: PointerEvent): number {
    const pointer = d3.pointer(event);
    let index = 0;
    const step = isVertical
      ? (gridState.xGridlineBottom as any).step()
      : (gridState.yGridlineLeft as any).step();

    if (dataLength > 1) {
      if (isVertical) {
        index = Math.round((pointer[0] - gridState.gridMargin.left) / step);
      } else {
        index = Math.round((pointer[1] - gridState.gridMargin.top) / step);
      }
    }
    // 若存在左右邊界，校正位置
    if (isPaddingOuter) {
      if (index > 0 && index < dataLength + 1) {
        index -= 1;
      } else if (index > dataLength) {
        index = dataLength - 1;
      }
    }
    return isVertical ? index : dataLength - index - 1;
  }

  /** 檢查當前 index 是否要顯示區間，並回傳資料 */
  function checkIndexAndData(index: number): DataSetWithDataArray[] {
    const shouldShow =
      gridConfig.showHoverArea &&
      (activeIndex !== index || !gridConfig.showActiveArea);
    d3.select(`#hover-area_${id}`).style('opacity', shouldShow ? '1' : '0');
    d3.selectAll(`#point-of-data_${id} circle`).style(
      'opacity',
      shouldShow ? '1' : '0'
    );

    return data.map((item) => {
      return {
        label: item.label,
        data: [item.data[index]],
        colors: item.colors,
      };
    });
  }

  /** 畫出區域 */
  function drawArea(
    index: number,
    step: number,
    isUsePointerEvent: boolean = true
  ): number {
    let position =
      dataLength < 2
        ? 0.5 * step
        : (isVertical ? index : dataLength - index - 1) * step;
    position += isVertical
      ? gridState.gridMargin.left
      : gridState.gridMargin.top;
    position = isPaddingOuter ? position + step : position;

    // 畫出 hover 區域
    const selector = isUsePointerEvent ? `#hover-area_${id}` : `path#active-area_${id}`;
    d3.select(selector)
      .attr('d', () => {
        let d = '';
        if (isVertical) {
          d =
            'M' +
            position +
            ',' +
            (gridState.gridSize.height + gridState.gridMargin.top);
          d += ' ' + position + ',' + gridState.gridMargin.top;
        } else {
          d =
            'M' +
            (gridState.gridSize.width + gridState.gridMargin.left) +
            ',' +
            position;
          d += ' ' + gridState.gridMargin.left + ',' + position;
        }
        return d;
      })
      .attr('stroke-width', () => {
        let width = gridConfig.hoverStrokeWidth < 0 ? step : gridConfig.hoverStrokeWidth;
        if (
          !isPaddingOuter &&
          (index == 0 || index == dataLength - 1)
        ) {
          width /= 2;
        }
        return `${width}px`;
      })
      .style('transform', () => {
        let translate = 0;
        const stepWidth = gridConfig.hoverStrokeWidth < 0 ? step: gridConfig.hoverStrokeWidth;
        if (!isPaddingOuter) {
          if (index == 0) {
            translate =  gridConfig.hoverStrokeWidth < 0 ? stepWidth / 4 : stepWidth / 2;
          } else if (index == dataLength - 1) {
            translate = gridConfig.hoverStrokeWidth < 0 ? -stepWidth / 4 : -stepWidth / 2;
          }
        }
        return `translate${isVertical ? 'X' : 'Y'}(${translate}px)`;
      })
      .style('opacity', 1);

    if (!isUsePointerEvent && gridConfig.showActiveArea) {
      setTimeout(() => {
        d3.select(`g#gridFactory_${id}`)
          .select(isVertical ? 'g#x' : 'g#y')
          .selectAll('g.tick')
          .filter((d, i) => i == index)
          .select('text')
          .style('fill', gridConfig.activeTextColor);
      }, 0);
    }
    return position;
  }
  //#endregion

  // 事件處理
  hoverEffectsGroup
    .append('svg:rect')
    .attr('class', 'hover-bounding')
    .attr('id', id)
    .attr('width', gridState.gridSize.width)
    .attr('height', gridState.gridSize.height)
    .attr('x', gridState.gridMargin.left)
    .attr('y', gridState.gridMargin.top)
    .attr('fill', 'none')
    .attr('pointer-events', 'all')
    .on('pointerout', (event: PointerEvent) => {
      d3.select(`#hover-area_${id}`).style('opacity', '0');
      d3.selectAll(`#point-of-data_${id} circle`).style('opacity', '0');
      lastPostion = -1;
      pointeroutCallback(event);
    })
    .on('pointerover', (event: PointerEvent) => {
      const index = getIndex(event);
      checkIndexAndData(index);
    })
    .on('pointermove', (event: PointerEvent) => {
      // const elements = document.elementsFromPoint(event.x, event.y);
      const index = getIndex(event);
      const position = drawArea(index, step);
      const hoverData = checkIndexAndData(index);
      lastIndex = index;
      const dataPointTransformArray: { x: number; y: number }[] = [];

      // hover key 軸上的點
      d3.selectAll(`#point-of-data_${id}`).attr(
        'transform',
        function (d: any, i) {
          // 變換顯示線條時改變tooltip
          if (lastPostion != position) {
            lastPostion = position;
          }

          let x = isVertical
            ? position
            : (gridState.xGridlineBottom(
                d.data[d.data.length - index - 1].value
              ) as number);

          let y = isVertical
            ? (gridState.y(d.data[index].value) as number) +
              gridState.gridMargin.top
            : position;

          x = isNaN(x) ? 0 : x;
          y = isNaN(y) ? 0 : y;
          const transform = `translate(${x},${y})`;
          // console.log(y)
          dataPointTransformArray.push({ x: x, y: y });
          return transform;
        }
      );

      //#region 計算最近的資料點
      const pointer = d3.pointer(event);
      const x = pointer[0];
      const y = pointer[1];
      let minDistance = Infinity;
      nearestIndex = -1;
      for (const [i, dataPointTransform] of dataPointTransformArray.entries()) {
        const dx = dataPointTransform.x - x;
        const dy = dataPointTransform.y - y;
        const distance = Math.sqrt(dx * dx + dy * dy);
        if (distance < minDistance) {
          minDistance = distance;
          nearestIndex = i;
        }
      }
      //#endregion
      hoverCallback(index, hoverData, nearestIndex, event);
    })
    .on('click', function (event: PointerEvent) {
      const index = getIndex(event);
      d3.select(`g#gridFactory_${id}`)
        .select(isVertical ? 'g#x' : 'g#y')
        .selectAll('g.tick')
        .filter((d, i) => i == activeIndex)
        .select('text')
        .style('fill', ColorType.AxisLabel);

      activeIndex = index;
      drawArea(index, step, false);
      if (!gridConfig.showActiveArea) {
        const selector = `path#active-area_${id}`;
        d3.select(selector).style('opacity', 0);
      }

      const clickData = checkIndexAndData(index);
      clickCallback(index, clickData, nearestIndex, event);
    });
}

/** 取得漂亮的最小值與最大值，且可平分為 n 個刻度 */
function getPrettyMinMax(min: number, max: number, stepsCount: number) {
  if (stepsCount < 2) return [min, max];
  const range = Math.abs(max - min);
  const rawStep = range / stepsCount; // 原始刻度一格單位長度
  const ticks: number[] = [];
  for (let i = 0; i <= stepsCount; i++) {
    ticks.push(rawStep * i + min);
  }
  // 取出ticks中最接近0的index
  const nearZeroIndex = ticks.reduce(
    (minIndex, item, index, src) =>
      Math.abs(item) < Math.abs(src[minIndex]) ? index : minIndex,
    0
  );
  // 計算正負比例，ex. ticks = [-3,2,7,12]; 正負比例為 1:2;
  let negativeRatio =
    nearZeroIndex + (nearZeroIndex == 0 && ticks[0] < 0 ? 1 : 0); // nearZeroIndex 為 0 且 ticks[0] < 0 需 + 1
  let positiveRatio = ticks.length - negativeRatio - 1;
  if (positiveRatio == 0 && ticks[ticks.length - 1] > 0) {
    positiveRatio += 1;
    negativeRatio -= 1;
  }

  const negativeStep = negativeRatio > 0 ? Math.abs(min / negativeRatio) : 0;
  const positiveStep = positiveRatio > 0 ? Math.abs(max / positiveRatio) : 0;
  const nAndPStep = Math.max(negativeStep, positiveStep); // 正負計算後的刻度一格單位長度
  const modifiedStep = Math.ceil(nAndPStep / 5) * 5; // step改成漂亮的數字(5的倍數)
  const modifiedMin = -modifiedStep * negativeRatio;
  const modifiedMax = modifiedStep * positiveRatio;

  return [modifiedMin, modifiedMax];
}
