import * as d3 from 'd3';
import { Observable, Subject } from 'rxjs';
import { Color } from '../../../../config/color.config';
import { FontConfig } from '../../../../config/font.config';
import { Direction } from '../../../../enum/enum';
import { getColorTypeArray, getSelectionColor } from '../../../../function/color.function';
import { deepCopy, getTextRect } from '../../../../function/common.function';
import { Data, DataSetWithDataArray } from '../../../../interface/common.interface';
import { BasicFactory } from '../../../basic-factory';
import { BasicTooltipFactory } from '../../../tooltip/basic-tooltip-factory';
import { TooltipFactory } from '../../../tooltip/tooltip-factory';
import { LineFactory } from '../line-factory/line-factory';
import { GridConfig } from './../../grid/grid.config';
import { DefaultDotConfig, DotConfig } from './dot.config';

/** 圓點組件，需繪製在 LineFactory 上 */
export class DotFactory extends BasicFactory<DotFactory> {
  private dotConfig: DotConfig = deepCopy(DefaultDotConfig);
  private dataColorArray: Color[][] = [];
  private showDotIndexes!: boolean[]; // 紀錄是否被設定過 showDotIndexes，若沒有則預設為DataSetWithDataArrays長度的true陣列
  private gridConfig: GridConfig;
  private pointerover$: Subject<any> = new Subject();
  private pointerout$: Subject<any> = new Subject();

  constructor(
    private lineFactory: LineFactory,
    private tooltipFactory: TooltipFactory<any> = new BasicTooltipFactory()
  ) {
    super();
    this.setLineFactory(lineFactory);
    this.gridConfig = this.lineFactory.getGridConfig();
  }

  /** 游標 hover 事件 */
  onPointerover(): Observable<any> {
    return this.pointerover$.asObservable();
  }

  /** 游標離開事件 */
  onPointerout(): Observable<any> {
    return this.pointerout$.asObservable();
  }

  setConfig(dotConfig: Partial<DotConfig>): DotFactory {
    Object.assign(this.dotConfig, dotConfig);
    return this;
  }

  /** 設定圓點半徑 */
  setRadius(radius: number): DotFactory {
    this.dotConfig.radius = radius;
    return this;
  }

  /** 設定是否顯示預設 tooltip */
  setShowTooltip(showTooltip: boolean): DotFactory {
    this.dotConfig.showTooltip = showTooltip;
    return this;
  }

  /** 設定要顯示的點的索引值 */
  setShowDotIndexes(showDotIndexes: boolean[]): DotFactory {
    this.showDotIndexes = showDotIndexes;
    this.dotConfig.showDotIndexes = showDotIndexes;
    return this;
  }

  /** 設定要顯示的 tooltip 種類 */
  setTooltipFactory(tooltipFactory: BasicTooltipFactory): DotFactory {
    this.tooltipFactory = tooltipFactory;
    return this;
  }

  /** 設定要繪製在哪個 LineFactory 上 */
  setLineFactory(lineFactory: LineFactory): DotFactory {
    this.lineFactory = lineFactory;
    const dataSetWithDataArrays = this.lineFactory.getDataSetWithDataArrays();
    this.dataColorArray = getColorTypeArray(dataSetWithDataArrays);
    this.dotConfig.showDotIndexes = this.showDotIndexes || dataSetWithDataArrays[0]?.data.map(() => true);
    return this;
  }

  /** 設定是否顯示數值標籤 */
  setShowValueLabel(showValueLabel: boolean): DotFactory {
    this.dotConfig.showValueLabel = showValueLabel;
    return this;
  }

  /** 設定數值標籤文字樣式 */
  setValueLabelFont(valueLabelFontSetting: Partial<FontConfig>): DotFactory {
    Object.assign(this.dotConfig.valueLabelFontSetting, valueLabelFontSetting);
    return this;
  }

  getSelector(): string {
    return `g#dot_${this.factoryId}`;
  }

  getConfig(): DotConfig {
    return this.dotConfig;
  }

  draw(useAnimation: boolean = false): void {
    const that = this;
    const isVertical = that.gridConfig.direction == Direction.vertical;

    this.lineFactory.getLineGroup().each(function (this: d3.BaseType, dSets: DataSetWithDataArray, i: number) {
      if (d3.select(`#dotFactory_${that.factoryId}_${i}`).empty()) {
        d3.select(this)
          .append('g')
          .attr('id', () => `dotFactory_${that.factoryId}_${i}`);
        d3.select(this)
          .append('g')
          .attr('id', () => `dot_valueLabel_group_${that.factoryId}_${i}`);
      }
      const dArray = dSets.data;
      const color = getSelectionColor(d3.select(this), that.dataColorArray[i], `dot_${that.factoryId}`, i);
      const setValueLabel = (selection: d3.Selection<any, Data, d3.BaseType, unknown>, j: number) => {
        if (!that.dotConfig.showValueLabel) return;
        if (that.dotConfig.showDotIndexes[j] && dArray[j].value !== null) {
          const parent = d3.select(`#dot_valueLabel_group_${that.factoryId}_${i}`);
          const id = `${that.factoryId}_${i}_${j}`;

          let textSelection: d3.Selection<any, unknown, HTMLElement, any> = parent.select(`#dot_valueLabel_${id}`);

          if (textSelection.empty()) {
            textSelection = parent.append('text').attr('id', `dot_valueLabel_${id}`);
          }

          const dataValueNumber = Number(dArray[j].value);
          const labelRect = getTextRect(
            dataValueNumber.toString(),
            that.dotConfig.valueLabelFontSetting['font-family'],
            that.dotConfig.valueLabelFontSetting['font-size'],
            that.dotConfig.valueLabelFontSetting['font-weight']
          );
          const gap = 8;
          const deltaX = isVertical ? -labelRect.width / 2 : dataValueNumber >= 0 ? gap : -labelRect.width;
          const deltaY = isVertical ? (dataValueNumber >= 0 ? -gap : labelRect.height) : labelRect.height / 2 - gap;

          textSelection
            .transition()
            .duration(useAnimation ? 500 : 0)
            .attr('x', () => that.lineFactory.getXPosition(dArray[j]) + deltaX)
            .attr('y', () => that.lineFactory.getYPosition(dArray[j]) + deltaY)
            .attr('fill', that.dotConfig.valueLabelFontSetting.color)
            .attr('font-family', that.dotConfig.valueLabelFontSetting['font-family'])
            .attr('font-size', that.dotConfig.valueLabelFontSetting['font-size'])
            .attr('font-weight', that.dotConfig.valueLabelFontSetting['font-weight'])
            .text(dataValueNumber);
        }
      };
      const setAttributes = (selection: d3.Selection<any, Data, d3.BaseType, unknown>, j: number) => {
        if (that.dotConfig.showDotIndexes[j] && dArray[j].value !== null) {
          selection
            .transition()
            .duration(useAnimation ? 500 : 0)
            .attr('cx', (d: Data) => that.lineFactory.getXPosition(d))
            .attr('cy', (d: Data) => that.lineFactory.getYPosition(d))
            .attr('r', that.dotConfig.radius)
            .attr('fill', color);
        }
      };
      const setEvents = (selection: d3.Selection<any, Data, d3.BaseType, unknown>) => {
        selection
          .on('pointerover', function (event: PointerEvent, d: Data) {
            d3.select(this)
              .transition()
              .duration(300)
              .ease(d3.easeCubicInOut)
              .attr('r', that.dotConfig.radius * 1.5);
            const element = event.currentTarget as HTMLElement;
            that.pointerover$.next(d);
            if (!that.dotConfig.showTooltip) return;
            that.tooltipFactory.setElement(element).setColor([that.dataColorArray[i]]).setData([d]).draw();
          })
          .on('pointermove', function (event: PointerEvent) {
            d3.select(this).dispatch('grid-hover', event);
          })
          .on('pointerout', function (event: PointerEvent, d: Data) {
            d3.select(this).transition().duration(300).attr('r', that.dotConfig.radius);
            that.pointerout$.next(d);
            if (!that.dotConfig.showTooltip) return;
            that.tooltipFactory.clear();
          });
      };

      const dataLength = dArray.length;
      d3.select(`#dotFactory_${that.factoryId}_${i}`)
        .selectAll('circle')
        .data(dArray)
        .join(
          enter =>
            enter
              .append('circle')
              .attr('id', (d, j) => `dot_${that.factoryId}_${i}_${j}`)
              .call(selection =>
                selection.each((d, i, node) => {
                  setAttributes(d3.select(node[i]), i);
                  setValueLabel(d3.select(node[i]), i);
                })
              )
              .call(setEvents)
              .style('opacity', 0)
              .call(enter =>
                enter
                  .transition('delayDot')
                  .delay((d, i) => (useAnimation ? (i * 500) / dataLength : 0))
                  .duration(useAnimation ? 800 : 0)
                  .style('opacity', 1)
              ),
          update =>
            update.call(update => {
              update
                .transition()
                .duration(useAnimation ? 500 : 0)
                .call(selection =>
                  selection.each((d, i, node) => {
                    if (d.value !== null) {
                      setAttributes(d3.select(node[i]), i);
                      setValueLabel(d3.select(node[i]), i);
                    } else {
                      selection.remove();
                    }
                  })
                );
              update.call(setEvents);
            }),
          exit => exit.call(exit => exit.transition().duration(500).style('opacity', 0)).remove()
        );
    });
  }

  clear(useAnimation: boolean = false): void {
    this.lineFactory
      .getLineGroup()
      ?.selectAll('circle')
      .transition()
      .duration(useAnimation ? 500 : 0)
      .style('opacity', 0)
      .remove();
  }
}
