import { D3Accessor } from "../enum/enum";
import { getRandomId } from "../function/common.function";
import { D3Operator } from "../interface/common.interface";

/**
 * 基礎組件工廠
 */
export abstract class BasicFactory<T> {
  protected isFirstUpdate = true;
  protected d3Operators!: D3Operator[];
  protected factoryId: string = getRandomId();

  setD3Operator(d3Operators: D3Operator[]): T {
    this.d3Operators = d3Operators;
    return this as unknown as T;
  }

  /** 取得組件 id */
  getId(): string {
    return this.factoryId;
  }

  protected ExtendD3(d3: any) {
    if (this.d3Operators) {
      for (const configItem of this.d3Operators) {
        switch (configItem[0]) {
          case D3Accessor.attr:
            d3.attr(configItem[1], configItem[2]);
            break;
          case D3Accessor.style:
            d3.style(configItem[1], configItem[2]);
            break;
        }
      }
    }
  }

  /** 設定組件中所有可以被設定的值 */
  abstract setConfig(config: Partial<any>): T;

  /** 取得組件中所有可以被設定的值 */
  abstract getConfig(): Record<string, any>;

  /** 取得組件選擇器 */
  abstract getSelector(): string;

  /** 繪製 */
  abstract draw(useAnimation: boolean): void;

  /** 清除繪製的內容 */
  abstract clear(useAnimation: boolean): void;
}
